import { useLayoutEffect } from 'react';
import { Helmet } from 'react-helmet';
import { appUrl, scrollToTop } from '../../utils';
import './PageTitle.scss';

export const PageTitle = ({ title, description, image }) => {

  scrollToTop();

  useLayoutEffect(() => {
    document.querySelector('.__page_title').scrollIntoView()
  })

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | {title}</title>
        <link rel="canonical" href={`${appUrl}/`} />
        <meta name="description" content={description} />
      </Helmet>

      <main className="__page_title w-100 bg-primary text-light">
        <div className="container-lg px-0 h-100">

          <div className="w-100 h-100 d-flex">
            <div className="w-50 h-100 d-flex justify-content-center align-items-center">
              <div className='w-70 d-none d-md-inline-block d-lg-inline-block'>
                <h1 className='bruno-ace fs-1 d-md-none d-lg-inline-block'>{title}</h1>
                <h1 className='bruno-ace fs-3 d-md-inline-block d-lg-none'>{title}</h1>
                <p className='mt-lg-4 d-none d-md-inline-block d-lg-inline-block'>{description}</p>
              </div>
              <div className='w-80 d-inline-block d-md-none d-lg-none'>
                <h1 className='bruno-ace fs-5'>{title}</h1>
              </div>
            </div>
            <div className="w-50 h-100 __page_image_container">
              <img className='__page_title_image' src={image} alt={title} />
            </div>
          </div>

        </div>
      </main>
    </>
  )
}