import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { alert, formatDate, formatMoney, formatNumber, query, request } from '../../../utils';
import { faEdit, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { useAuthStore } from '../../../store/auth';
import { useCallback } from 'react';
import { DashboardLayout } from '../../../components';


export const ReferralLevels = () => {
  const [referrallevels, setReferralLevels] = useState([])
  const [editingLevel, setEditingLevel] = useState({})
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getReferralLevels = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/referrallevels', 'GET', null, headers);

      if (resp.status === true) {
        setReferralLevels(resp.data)
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  useEffect(() => {
    getReferralLevels();
    setPageIsLoading(false)
  }, [getReferralLevels])

  const addReferralLevelModal = () => {
    return (
      <div>
        <div className="modal fade" id="addReferralLevelModal" tabIndex={-1} aria-labelledby="addReferralLevelModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addReferralLevelModalLabel">Editing {editingLevel.rank} Rank</h5>
                <button id="addReferralLevelModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={updateReferralLevel}>
                <div className="modal-body">
                  <div className="mb-3">
                    <label htmlFor="referralrank" className="form-label">Rank</label>
                    <input type="text" readOnly={true} autoComplete={""} className="form-control" id="referralrank" placeholder="Referral Level Rank" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralrequired" className="form-label">No. of referrals required</label>
                    <input type="number" className="form-control form-control" id="referralrequired" placeholder="no. of referrals required" />
                  </div>

                  <br />
                  <h5>Benefits</h5>
                  <hr />

                  <div className='row'>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>Local Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_local" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_local" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>Foreign Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_foreign" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_foreign" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                  </div>

                  <div className='row'>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>International Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_international" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_international" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>Continental Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_continental" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_continental" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                  </div>

                  <div className='row'>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>World Cup Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_worldcup" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_worldcup" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                    <div className='col-lg-6 col-md-6 col-12'>
                      <h6 className='fw-500'>Olympic Benefits</h6>
                      <div className="mb-3">
                        <input type="" autoComplete={""} className="form-control" id="referralcashbenefit_olympic" placeholder="Cash benefit" />
                      </div>
                      <div className="mb-3">
                        <textarea id="referralsouvenirbenefit_olympic" cols="30" rows="3" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                      </div>
                    </div>
                  </div>

                </div>
                <div className="modal-footer">
                  <button type="submit" className="btn btn-primary px-5">Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const editReferralLevel = (referrallevel) => {
    setEditingLevel(referrallevel)
    query('#referralrank')[0].value = referrallevel.rank;
    query('#referralrequired')[0].value = referrallevel.referrals_required;

    query('#referralcashbenefit_local')[0].value = referrallevel.benefits[0].cash ?? 0;
    query('#referralsouvenirbenefit_local')[0].value = referrallevel.benefits[0].souvenir ?? "";
    query('#referralcashbenefit_foreign')[0].value = referrallevel.benefits[1].cash ?? 0;
    query('#referralsouvenirbenefit_foreign')[0].value = referrallevel.benefits[1].souvenir ?? "";
    query('#referralcashbenefit_international')[0].value = referrallevel.benefits[2].cash ?? 0;
    query('#referralsouvenirbenefit_international')[0].value = referrallevel.benefits[2].souvenir ?? "";
    query('#referralcashbenefit_continental')[0].value = referrallevel.benefits[3].cash ?? 0;
    query('#referralsouvenirbenefit_continental')[0].value = referrallevel.benefits[3].souvenir ?? "";
    query('#referralcashbenefit_worldcup')[0].value = referrallevel.benefits[4].cash ?? 0;
    query('#referralsouvenirbenefit_worldcup')[0].value = referrallevel.benefits[4].souvenir ?? "";
    query('#referralcashbenefit_olympic')[0].value = referrallevel.benefits[5].cash ?? 0;
    query('#referralsouvenirbenefit_olympic')[0].value = referrallevel.benefits[5].souvenir ?? "";

    query('#addReferralLevelModalToggle')[0].click();
  }

  const noReferralLevels = () => {
    return <tr>
      <td colSpan={7} className='text-center'>No Referral Levels.</td>
    </tr>
  }

  const renderReferralLevels = () => {
    return referrallevels.map((referrallevel, index) => (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{referrallevel.rank}</td>
        <td>{formatNumber(referrallevel.referrals_required)} persons</td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[0].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[0].souvenir}
        </td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[1].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[1].souvenir}
        </td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[2].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[2].souvenir}
        </td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[3].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[3].souvenir}
        </td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[4].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[4].souvenir}
        </td>
        <td>
          <b className='fw-500'>Cash:</b> {formatMoney(referrallevel.benefits[5].cash, 0)} <br />
          <b className='fw-500'>Souvenir:</b> {referrallevel.benefits[5].souvenir}
        </td>
        <td>{formatDate(referrallevel.updated_at) ?? ""}</td>
        <td>
          <FontAwesomeIcon className="mx-2" title={`edit ${referrallevel.rank}`} style={{ cursor: "pointer" }} icon={faEdit} onClick={() => editReferralLevel(referrallevel)}></FontAwesomeIcon>
        </td>
      </tr>
    ))
  }

  const updateReferralLevel = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let rank = query('#referralrank')[0];
    let referrals_required = query('#referralrequired')[0];
    // let cash_benefit = query('#referralcashbenefit')[0];
    // let benefits = query('#referralsouvenirbenefit')[0];

    let local_cash = query('#referralcashbenefit_local')[0];
    let local_souvenir = query('#referralsouvenirbenefit_local')[0];
    let foreign_cash = query('#referralcashbenefit_foreign')[0];
    let foreign_souvenir = query('#referralsouvenirbenefit_foreign')[0];
    let international_cash = query('#referralcashbenefit_international')[0];
    let international_souvenir = query('#referralsouvenirbenefit_international')[0];
    let continental_cash = query('#referralcashbenefit_continental')[0];
    let continental_souvenir = query('#referralsouvenirbenefit_continental')[0];
    let worldcup_cash = query('#referralcashbenefit_worldcup')[0];
    let worldcup_souvenir = query('#referralsouvenirbenefit_worldcup')[0];
    let olympic_cash = query('#referralcashbenefit_olympic')[0];
    let olympic_souvenir = query('#referralsouvenirbenefit_olympic')[0];

    let body = new FormData();
    body.append('id', editingLevel.id);
    body.append('rank', rank.value);
    body.append('referrals_required', referrals_required.value);

    // body.append('cash_benefit', cash_benefit.value);
    // body.append('benefits', benefits.value);

    body.append('local_benefit_id', editingLevel.benefits[0].benefit_id);
    body.append('local_cash', local_cash.value);
    body.append('local_souvenir', local_souvenir.value);
    body.append('foreign_benefit_id', editingLevel.benefits[1].benefit_id);
    body.append('foreign_cash', foreign_cash.value);
    body.append('foreign_souvenir', foreign_souvenir.value);
    body.append('international_benefit_id', editingLevel.benefits[2].benefit_id);
    body.append('international_cash', international_cash.value);
    body.append('international_souvenir', international_souvenir.value);
    body.append('continental_benefit_id', editingLevel.benefits[3].benefit_id);
    body.append('continental_cash', continental_cash.value);
    body.append('continental_souvenir', continental_souvenir.value);
    body.append('worldcup_benefit_id', editingLevel.benefits[4].benefit_id);
    body.append('worldcup_cash', worldcup_cash.value);
    body.append('worldcup_souvenir', worldcup_souvenir.value);
    body.append('olympic_benefit_id', editingLevel.benefits[5].benefit_id);
    body.append('olympic_cash', olympic_cash.value);
    body.append('olympic_souvenir', olympic_souvenir.value);


    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/referrallevels/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Referral level updated successfully`, 'success');
        setReferralLevels(resp.data)
        query("#addReferralLevelModalDismissBtn")[0].click();
        referrals_required.value = "";
        // cash_benefit.value = ""; benefits.value = "";

        local_cash.value = ""; local_souvenir.value = ""; foreign_cash.value = ""; foreign_souvenir.value = ""; international_cash.value = ""; international_souvenir.value = ""; continental_cash.value = ""; continental_souvenir.value = ""; worldcup_cash.value = ""; worldcup_souvenir.value = ""; olympic_cash.value = ""; olympic_souvenir.value = "";

      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  return (
    <>
      <DashboardLayout page='/referral-levels'>
        <section className='container my-3 px-3'>

          <div className='mb-3'>
            <h1 className='fw-normal fs-2'>Referral Levels</h1>
            <hr className='my-0' />
          </div>
          <button id='addReferralLevelModalToggle' className="d-none btn btn-sm btn-outline-secondary" data-bs-toggle="modal" data-bs-target="#addReferralLevelModal"></button>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div>
                  {
                    referrallevels.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      There are no Referral Levels
                    </div>
                  }
                </div>

                {referrallevels.length > 0 &&
                  <>
                    <div className="table-responsive">
                      <table className="table table-striped table-sm">
                        <thead>
                          <tr>
                            <th className='fw-500'>#</th>
                            <th className='fw-500'>Rank</th>
                            <th className='fw-500'>Referrals&nbsp;Required</th>
                            <th className='fw-500'>Local&nbsp;Benefit</th>
                            <th className='fw-500'>Foreign&nbsp;Benefit</th>
                            <th className='fw-500'>International&nbsp;Benefit</th>
                            <th className='fw-500'>Continental&nbsp;Benefit</th>
                            <th className='fw-500'>World&nbsp;Cup&nbsp;Benefit</th>
                            <th className='fw-500'>Olympic&nbsp;Benefit</th>
                            <th className='fw-500'>Date&nbsp;Updated</th>
                            <th className='fw-500'></th>
                          </tr>
                        </thead>
                        <tbody>
                          {referrallevels.length > 0 ? renderReferralLevels() : noReferralLevels()}
                        </tbody>
                      </table>
                    </div>
                  </>
                }

                {addReferralLevelModal()}

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
