import React, { useCallback, useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { useAuthStore } from './store/auth';
import { useUserStore } from './store/user';
import { AboutUs, ContactUs, FAQs, ForgotPassword, HowItWorks, Login, Page404, PrivacyPolicy, Register, TermsAndConditions, Welcome } from './pages/public';
import { Dashboard, MyBenefits, MyDownlines, Notifications, PayInvoice, Profile, Slots, Verify } from './pages/users';
import { AdminNotifications, Competitions, Invoices, Payments, ReferralLevels, SlotPackages, UserBenefits, UserDownlines, UserProfile, Users, UserSlots } from './pages/admin';
import { AuthMiddleware } from './components';

function App() {

  const { boot: bootAuth } = useAuthStore();
  const { boot: bootUser } = useUserStore();
  const [booted, setBooted] = useState(false);

  const boot = useCallback(async function boot() {
    await bootAuth();
    await bootUser();
    setBooted(true)
  }, [bootAuth, bootUser])

  useEffect(() => {
    boot();
  }, [boot])

  return (
    <>
      {/* Modal */}
      <div style={{ zIndex: 9999 }} className='w-100 h-auto position-relative' >
        {/* Button trigger modal */}
        <button id="confirmModalTrigger" type="button" className="d-none" data-bs-toggle="modal" data-bs-target="#confirmModal"></button>

        <div className="modal fade" id="confirmModal" tabIndex={-1} aria-hidden="true" data-bs-backdrop="static">
          <div className="modal-dialog">
            <button id="confirmModalDismissTrigger" type="button" className="d-none btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            <div id="confirmModalContent" className="modal-content"></div>
          </div>
        </div>
      </div>

      {/* Alert */}
      <div style={{ zIndex: 9999 }} className='w-100 h-auto position-fixed'>
        <div id="notify" className="position-relative w-100">
          {/* Alert */}
          <div id="alertContainer" style={{ zIndex: 9999 }} className="toast-container position-absolute top-0 end-0 mt-3 me-lg -3 me-2"></div>
        </div>
      </div>

      {booted ?
        <Router>
          <Switch>

            {/* Public Routes */}
            <Route exact path='/' component={Welcome} />
            <Route exact path='/about-us' component={AboutUs} />
            <Route exact path='/how-it-works' component={HowItWorks} />
            <Route exact path='/faq' component={FAQs} />
            <Route exact path='/contact-us' component={ContactUs} />
            <Route exact path='/terms-and-conditions' component={TermsAndConditions} />
            <Route exact path='/privacy-policy' component={PrivacyPolicy} />
            <Route exact path='/register' component={Register} />
            <Route exact path='/login' component={Login} />
            <Route exact path='/forgot-password' component={ForgotPassword} />
            {/* <Route exact path='/reset-password' component={ResetPassword} /> */}

            {/* Authenticated Routes */}
            <Route exact path='/dashboard'>
              <AuthMiddleware role={['member', 'admin']}>
                <Dashboard />
              </AuthMiddleware>
            </Route>

            <Route exact path='/profile'>
              <AuthMiddleware role={['member', 'admin']}>
                <Profile />
              </AuthMiddleware>
            </Route>

            <Route exact path='/slots'>
              <AuthMiddleware role={['member', 'admin']}>
                <Slots />
              </AuthMiddleware>
            </Route>

            <Route exact path='/downlines'>
              <AuthMiddleware role={['member', 'admin']}>
                <MyDownlines />
              </AuthMiddleware>
            </Route>

            <Route exact path='/benefits'>
              <AuthMiddleware role={['member', 'admin']}>
                <MyBenefits />
              </AuthMiddleware>
            </Route>

            <Route exact path='/notifications'>
              <AuthMiddleware role={['member', 'admin']}>
                <Notifications />
              </AuthMiddleware>
            </Route>

            <Route exact path='/payment-invoice'>
              <AuthMiddleware role={['member', 'admin']}>
                <PayInvoice />
              </AuthMiddleware>
            </Route>

            <Route exact path='/verify-account'>
              <AuthMiddleware role={['member', 'admin']}>
                <Verify />
              </AuthMiddleware>
            </Route>

            {/* ---------------------------------------------------- */}

            <Route exact path='/users'>
              <AuthMiddleware role={['admin']} permissions={'users'}>
                <Users />
              </AuthMiddleware>
            </Route>

            <Route exact path='/users/profile'>
              <AuthMiddleware role={['admin']} permissions={'users'}>
                <UserProfile />
              </AuthMiddleware>
            </Route>

            <Route exact path='/users/downlines'>
              <AuthMiddleware role={['admin']} permissions={'downlines'}>
                <UserDownlines />
              </AuthMiddleware>
            </Route>

            <Route exact path='/invoices'>
              <AuthMiddleware role={['admin']} permissions={'invoices'}>
                <Invoices />
              </AuthMiddleware>
            </Route>

            <Route exact path='/payments'>
              <AuthMiddleware role={['admin']} permissions={'payments'}>
                <Payments />
              </AuthMiddleware>
            </Route>

            <Route exact path='/competitions'>
              <AuthMiddleware role={['admin']} permissions={'competitions'}>
                <Competitions />
              </AuthMiddleware>
            </Route>

            <Route exact path='/slot-packages'>
              <AuthMiddleware role={['admin']} permissions={'slot packages'}>
                <SlotPackages />
              </AuthMiddleware>
            </Route>

            <Route exact path='/referral-levels'>
              <AuthMiddleware role={['admin']} permissions={'referral levels'}>
                <ReferralLevels />
              </AuthMiddleware>
            </Route>

            <Route exact path='/users/benefits'>
              <AuthMiddleware role={['admin']} permissions={'member benefits'}>
                <UserBenefits />
              </AuthMiddleware>
            </Route>

            <Route exact path='/admin-notifications'>
              <AuthMiddleware role={['admin']} permissions={'notifications'}>
                <AdminNotifications />
              </AuthMiddleware>
            </Route>

            <Route exact path='/users/slots'>
              <AuthMiddleware role={['admin']} permissions={'slot packages'}>
                <UserSlots />
              </AuthMiddleware>
            </Route>

            {/* Fallback Route */}
            <Route component={Page404} />

          </Switch>
        </Router>
        :
        <div>
          {/* TODO: replace with loading component page  */}
          Loading app...
        </div>
      }

    </>
  );

}

export default App;
