import { faUsers, faCrown, faEdit, faExclamation, faInfoCircle, faServer, faShieldAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState, useCallback } from 'react';
import { alert, baseUrl, formatDate, query, request } from '../../../utils';
import defaultProfilePicture from '../../../assets/image/default_profile_picture.png';
import { Link, useHistory } from 'react-router-dom';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';

export const Users = () => {

  const [allUsers, setAllUsers] = useState([])
  const [members, setMembers] = useState([])
  const [admins, setAdmins] = useState([])
  const [limit] = useState(10)
  const [membersPage, setMembersPage] = useState(1)
  const [viewMembers, setViewMembers] = useState([])
  const [searchMembers, setSearchMembers] = useState([])
  const [memberPrivilege, setMemberPrivilege] = useState({
    id: 0,
    firstname: '',
    role: '',
    privileges: []
  })
  const [memberNotice, setMemberNotice] = useState({
    id: 0,
    firstname: '',
    isGroup: false
  })
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const [isSubmitting] = useState(false)
  const { auth } = useAuthStore()
  const history = useHistory()

  const getAllUsers = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/all-users', 'GET', null, headers);

      if (resp.status === true) {
        let allUsers = resp.data;
        setAllUsers(allUsers)
        let allMembers = allUsers.filter((user) => { user.selected = false; return user.role === 'member' })
        setMembers(allMembers)
        setAdmins(allUsers.filter((user) => { user.selected = false; return user.role === 'admin' }))
        
        setViewMembers(allMembers)
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  const updateStatus = async (e, member, index) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let status = query(`#status${index}`)[0];
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let body = new FormData();
      body.append('userId', member.id);
      body.append('status', status.value);
      let resp = await request('/api/admin/update-status', 'POST', body, headers);

      if (resp.status === true) {
        alert('Status updated successfully', 'success');
        getAllUsers();
      } else {
        alert('Status updated failed', 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  const handleMemberSearch = (e) => {
    e.preventDefault();
    e.stopPropagation();

    let search = query('#memberSearchbox')[0];
    let searchmembers = members.filter((member) => {

      if (`${member.firstname != null ? member.firstname.toLowerCase() : ''} ${member.lastname != null ? member.lastname.toLowerCase() : ''} ${member.middlename != null ? member.middlename.toLowerCase() : ''}`.includes(search.value.toLowerCase())) return member;

      if (member.email === search.value) return member;

      if (member.referral_code === search.value) return member;

      if (member.member_id === search.value) return member;

      return false;

    });

    setViewMembers(searchmembers)
    setSearchMembers(searchmembers)
  }

  const editPrivilege = (id, firstname, role, privileges) => {
    setMemberPrivilege({
      id: id,
      firstname: firstname,
      role: role,
      privileges: JSON.parse(privileges) ?? []
    })
    query("#editPrivilegeModalTrigger")[0].click();
  }

  const updatePrivilege = async (e, member, index) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let body = new FormData();
      body.append('userId', memberPrivilege.id);
      body.append('role', memberPrivilege.role);
      body.append('privileges', JSON.stringify(memberPrivilege.privileges));
      let resp = await request('/api/admin/update-privileges', 'POST', body, headers);

      if (resp.status === true) {
        query('#editPrivilegeModalDismissBtn')[0].click();
        alert(resp.message, 'success');
        getAllUsers();
      } else {
        alert(resp.message, 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  const editPrivilegeModal = () => {
    return (
      <>
        <button className="btn btn-sm btn-outline-grey d-none" id="editPrivilegeModalTrigger" data-bs-toggle="modal" data-bs-target="#editPrivilegeModal"></button>
        <div>
          <div className="modal fade" id="editPrivilegeModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="editPrivilegeModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="editPrivilegeModalLabel">Edit {memberPrivilege.firstname}'s Privileges</h5>
                  <button id="editPrivilegeModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <form onSubmit={updatePrivilege}>
                  <div className="modal-body">

                    <div className="mb-3">
                      <h6>Role</h6>

                      <div className="btn-group w-100">
                        {
                          ['member', 'admin'].map((role, index) => (
                            <div key={index} className={`btn p-0 btn-outline-grey text-dark ${memberPrivilege.role === role && 'btn-grey'}`}>
                              <input disabled={isSubmitting} type="radio" name="role" defaultChecked={memberPrivilege.role === role} className="btn-check" id={'role' + index} autoComplete="off" />
                              <label className="w-100" onClick={() => setMemberPrivilege({ ...memberPrivilege, role: role })} htmlFor={'role' + index}>
                                <div className='fs-6 p-2 text-center'>
                                  {role.toUpperCase()}
                                </div>
                              </label>
                            </div>
                          ))
                        }
                      </div>
                    </div>

                    <div className="mb-3">
                      <h6>Privileges</h6>

                      <div className="row">
                        {
                          [
                            'users', 'downlines', 'invoices', 'payments', 'competitions', 'slot packages',
                            'referral levels', 'member benefits', 'notifications'].map((privilege, index) => (
                              <div key={index} className="col-lg-4 col-md-6 col-12 mb-3 form-check form-switch p-0">
                                <input onChange={() => { }} disabled={isSubmitting} type="checkbox" checked={memberPrivilege.privileges.includes(privilege) === true} id={'privilege' + index} autoComplete="off" className='form-check-input ms-2 ms-lg-3 me-2' value={privilege} />
                                <label onClick={() => {
                                  console.log(memberPrivilege.privileges.includes(privilege))
                                  if (!memberPrivilege.privileges.includes(privilege)) {
                                    memberPrivilege.privileges.push(privilege);
                                    setMemberPrivilege({
                                      ...memberPrivilege,
                                      privileges: memberPrivilege.privileges
                                    })
                                  } else {
                                    memberPrivilege.privileges.splice(memberPrivilege.privileges.indexOf(privilege), 1);
                                    setMemberPrivilege({
                                      ...memberPrivilege,
                                      privileges: memberPrivilege.privileges
                                    })
                                  }
                                }} htmlFor={'privilege' + index}>
                                  <div className='fs-6'>
                                    {privilege.toUpperCase()}
                                  </div>
                                </label>
                              </div>
                            ))
                        }
                      </div>

                    </div>

                  </div>
                  <div className="modal-footer">
                    <button type="submit" className="btn btn-primary px-5">Update</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  const selectAdmin = (selected) => {
    let selectedAdmin = admins.find((member) => { return member.id === selected.id });
    selectedAdmin['selected'] = !selectedAdmin.selected ?? true;
    setAdmins([...admins])
  }

  const selectMember = (selected) => {
    let selectedMember = members.find((member) => { return member.id === selected.id });
    selectedMember['selected'] = !selectedMember.selected ?? true;
    setMembers([...members])
  }

  const sendNotice = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let resp;
      if (memberNotice.isGroup === true) {

        let selected = [];
        if (memberNotice.firstname === 'Members')
          members.filter(member => member.selected === true).forEach(i => selected.push(i.id));
        else if (memberNotice.firstname === 'Admins')
          admins.filter(member => member.selected === true).forEach(i => selected.push(i.id));
        else return

        let body = new FormData();
        body.append('userIds', JSON.stringify(selected));
        body.append('message', query('#noticeMessage')[0].value);
        resp = await request('/api/admin/send-group-notice', 'POST', body, headers);
      } else {
        let body = new FormData();
        body.append('userId', memberNotice.id);
        body.append('message', query('#noticeMessage')[0].value);
        resp = await request('/api/admin/send-notice', 'POST', body, headers);
      }

      if (resp.status === true) {
        query('#sendNoticeModalDismissBtn')[0].click();
        alert("Notice sent", 'success');
      } else {
        alert('Notice not sent', 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  const sendSingleNotice = (id, firstname) => {
    setMemberNotice({
      id: id,
      firstname: firstname,
      isGroup: false
    })
    query("#sendNoticeModalTrigger")[0].click();
  }

  const sendNoticeToMany = (group) => {
    setMemberNotice({
      firstname: group,
      isGroup: true
    })
    query("#sendNoticeModalTrigger")[0].click();
  }

  const sendNoticeModal = () => {
    return (
      <>
        <button className="btn btn-sm btn-outline-grey d-none" id="sendNoticeModalTrigger" data-bs-toggle="modal" data-bs-target="#sendNoticeModal"></button>
        <div>
          <div className="modal fade" id="sendNoticeModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="sendNoticeModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="sendNoticeModalLabel">Send Notice to {memberNotice.firstname}</h5>
                  <button id="sendNoticeModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <form onSubmit={sendNotice}>
                  <div className="modal-body">

                    <div className="mb-3">
                      <h6>Message</h6>

                      <div className="">
                        <textarea className="form-control" disabled={isSubmitting} placeholder="Leave a comment here" id="noticeMessage" style={{ height: '100px' }} defaultValue={""} required />
                      </div>

                    </div>

                  </div>
                  <div className="modal-footer">
                    <button type="submit" className="btn btn-primary px-5">Send</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  useEffect(() => {
    getAllUsers();
    setPageIsLoading(false)
  }, [getAllUsers])

  return (
    <>
      <DashboardLayout page='/users'>
        <section className='container my-3 px-3'>

          <div className='mb-5'>
            <h1 className='fw-normal fs-2'>Accounts</h1>
            <span>There are a total of {allUsers.length} accounts</span>
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-grey">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div>
                  {
                    allUsers.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      There are no Accounts
                    </div>
                  }
                </div>

                {allUsers.length > 0 &&
                  <>

                    <div className='mb-5'>
                      <div className='mb-4 row'>
                        <div className='col-12 col-lg-6 mb-lg-0 mb-2'>
                          <h4 className='lh-0 my-0 fw-normal'>Administrators</h4>
                          <small>{admins.length > 1 ? `There are ${admins.length} Administrators accounts` : `There is only one Administrator`}</small>
                        </div>

                        <div className='col-12 col-lg-6 mb-lg-0 mb-2 clearfix'>
                          <button disabled={
                            admins.every((admin) => { return admin.selected === false })
                          } className='btn btn-sm btn-primary float-end' onClick={() => sendNoticeToMany('Admins')}>
                            <FontAwesomeIcon className='text-light' icon={faInfoCircle}></FontAwesomeIcon> Notify Selected Admins
                          </button>
                        </div>
                        {/* <hr className='my-0' /> */}
                      </div>

                      <div>
                        {
                          admins.length === 0 &&
                          <div className='my-5 py-5 d-flex flex-column align-items-center'>
                            <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                            There are no Administrators
                          </div>
                        }
                      </div>

                      {admins.length > 0 &&
                        <>
                          <div className='table-responsive w-100'>
                            <table className='table table-hover'>
                              <thead>
                                <tr>
                                  <th className='fw-500'>#</th>
                                  <th className='fw-500'>Admin</th>
                                  <th className='fw-500'>Program</th>
                                  <th className='fw-500'>Rank</th>
                                  <th className='fw-500'>Email</th>
                                  <th className='fw-500'>Telephone</th>
                                  <th className='fw-500'>Registered</th>
                                  <th className='fw-500'>Payment</th>
                                  <th className='fw-500'>Status</th>
                                  <th className='fw-500'>Actions</th>
                                </tr>
                              </thead>

                              <tbody>
                                {/* .slice(((membersPage * limit) - limit), (membersPage * limit)) */}
                                {admins.map((member, index) => (
                                  <tr key={index}>
                                    <td className='align-middle'>
                                      <div className="form-check">
                                        <input onChange={() => selectAdmin(member)} checked={member.selected} className="form-check-input" type="checkbox" id={`admin${index}`} />
                                        <label className="form-check-label" htmlFor={`admin${index}`}>{index + 1 + (limit * (membersPage - 1))}</label>
                                      </div>
                                    </td>
                                    <td className='align-middle'>
                                      <div className='align-top'>
                                        <div className='d-flex'>
                                          <div className='me-2' onClick={() => { history.push('/users/profile?u=' + member.email) }} style={{ cursor: 'pointer' }} title={`Click to view ${member.firstname ?? 'No name'}'s profile`}>
                                            <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                          </div>
                                          <div>
                                            {
                                              member.firstname === null && member.lastname === null && member.middlename === null
                                                ? <span>No&nbsp;name</span> :
                                                <span>{member.firstname}&nbsp;{member.lastname}&nbsp;{member.middlename}</span>
                                            } <br />
                                            <small>
                                              {
                                                <span title='Member ID'>{member.member_id}</span> ?? <span>No&nbsp;Member&nbsp;ID</span>
                                              }
                                              <span className='text-dark fw-bold mx-1'></span>
                                              {
                                                <span title='Referral Code'>{member.referral_code}</span> ?? 'No referral code'
                                              }
                                            </small>
                                          </div>
                                        </div>
                                      </div>
                                    </td>
                                    <td className='align-middle'>
                                      {member.slot_program} Program
                                    </td>
                                    <td className='align-middle'>
                                      {member.rank}
                                    </td>
                                    <td className='align-middle'>
                                      {member.email}
                                    </td>
                                    <td className='align-middle'>
                                      {member.telephone ?? <span>No&nbsp;telephone</span>}
                                    </td>
                                    <td className='align-middle'>
                                      {formatDate(member.created_at)}
                                    </td>
                                    <td className='align-middle'>
                                      <small className={member.payment_status === 'active' ? 'text-success' : 'text-danger'}>{member.payment_status === 'active' ? 'PAID' : 'UNPAID'}</small>
                                    </td>
                                    <td className='align-middle'>
                                      <div style={{ width: '100px' }} className='d-flex align-items-center'>
                                        <div>{member.verification_status}</div>
                                        <FontAwesomeIcon style={{ cursor: 'pointer' }} onClick={() => {
                                          let editstatus = query(`#editadminstatus${index}`)[0];
                                          editstatus.hidden = !editstatus.hidden;
                                        }} className='text-primary ms-2' title={`Update ${member.firstname ?? 'No name'}'s status`} icon={faEdit}></FontAwesomeIcon>
                                      </div>

                                      <form id={`editadminstatus${index}`} hidden={true} onSubmit={(e) => updateStatus(e, member, index)}>
                                        <div className="form-floating mt-2">
                                          <select className="form-control" id={`adminstatus${index}`} defaultValue={member.verification_status}>
                                            <option value="verified">verified</option>
                                            <option value="blocked">blocked</option>
                                            <option value="closed">closed</option>
                                          </select>
                                          <label htmlFor={`adminstatus${index}`} className=''>select&nbsp;status</label>
                                          <button type='submit' className='btn mt-2 w-100 btn-sm btn-primary'>update</button>
                                        </div>
                                      </form>

                                    </td>
                                    <td className='align-middle'>
                                      <div className='d-flex'>
                                        <button onClick={() => sendSingleNotice(member.id, member.firstname)} className='btn btn-sm btn-muted px-' title={`Send notice to ${member.firstname ?? 'No name'}`}><FontAwesomeIcon className='text-primary' icon={faInfoCircle}></FontAwesomeIcon></button>

                                        <Link to={`/users/downlines?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s downlines`}><FontAwesomeIcon className='text-primary' icon={faUsers}></FontAwesomeIcon></Link>

                                        <Link to={`/users/slots?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s referrals`}><FontAwesomeIcon className='text-primary' icon={faServer}></FontAwesomeIcon></Link>

                                        <Link to={`/users/benefits?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s benefits`}><FontAwesomeIcon className='text-primary' icon={faCrown}></FontAwesomeIcon></Link>

                                        <button onClick={() => editPrivilege(member.id, member.firstname, member.role, member.permissions)} className='btn btn-sm btn-muted' title={`Edit ${member.firstname ?? 'No name'}'s privileges`}><FontAwesomeIcon className='text-primary' icon={faShieldAlt}></FontAwesomeIcon></button>
                                      </div>
                                    </td>
                                  </tr>
                                ))
                                }
                              </tbody>

                            </table>
                          </div>
                        </>
                      }
                    </div>

                    <div className='mb-5'>
                      <div className='mb-4 row'>
                        <div className='col-12 col-lg-6 mb-lg-0 mb-2'>
                          <h4 className='lh-0 my-0 fw-normal'>Members</h4>
                          <small>{members.length > 1 ? `There are ${members.length} member accounts` : `There is only one Member`}</small>
                        </div>

                        <div className='col-12 col-lg-6'>
                          <form onSubmit={e => e.preventDefault()}>
                            <input type="search" id="memberSearchbox" onChange={handleMemberSearch} className='form-control form-control w-100' placeholder='search with name, email, member Id or referral code' />
                            <small>{searchMembers.length} results found</small>
                          </form>
                        </div>
                      </div>

                      <div>
                        {
                          members.length === 0 &&
                          <div className='my-5 py-5 d-flex flex-column align-items-center'>
                            <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                            There are no Members
                          </div>
                        }
                      </div>

                      {members.length > 0 &&
                        <>
                          <div className='table-responsive w-100'>
                            <table className='table'>
                              <thead>
                                <tr>
                                  <th>
                                    <div className="form-check">
                                      <input title="select all"
                                        checked={members.every(member => member.selected === true)}
                                        onChange={async (e) => {
                                          members.forEach(member => member.selected = e.target.checked);
                                          setMembers([...members])
                                        }}
                                        className="form-check-input d-inline-block" type="checkbox" id='selectAllMembers' />
                                      <label className="form-check-label fw-500" htmlFor='selectAllMembers'>#</label>
                                    </div>
                                  </th>
                                  <th className='fw-500'>Member</th>
                                  <th className='fw-500'>Program</th>
                                  <th className='fw-500'>Rank</th>
                                  <th className='fw-500'>Email</th>
                                  <th className='fw-500'>Telephone</th>
                                  <th className='fw-500'>Registered</th>
                                  <th className='fw-500'>Payment</th>
                                  <th className='fw-500'>Status</th>
                                  <th className='fw-500'>Actions</th>
                                </tr>
                              </thead>

                              <tbody>
                                {viewMembers.slice(((membersPage * limit) - limit), (membersPage * limit)).map((member, index) => (
                                  <tr key={index} >
                                    <td className='align-middle'>
                                      <div className="form-check">
                                        <input onChange={() => selectMember(member)} checked={member.selected} className="form-check-input" type="checkbox" id={`member${index}`} />
                                        <label className="form-check-label" htmlFor={`member${index}`}>{index + 1 + (limit * (membersPage - 1))}</label>
                                      </div>
                                    </td>
                                    <td className='align-middle'>
                                      <div className='align-top'>
                                        <div className='d-flex'>
                                          <div className='me-2' onClick={() => { history.push('/users/profile?u=' + member.email) }} style={{ cursor: 'pointer' }} title={`Click to view ${member.firstname ?? 'No name'}'s profile`}>
                                            <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                          </div>
                                          <div>
                                            {
                                              member.firstname === null && member.lastname === null && member.middlename === null
                                                ? <span>No&nbsp;name</span> :
                                                <span>{member.firstname}&nbsp;{member.lastname}&nbsp;{member.middlename}</span>
                                            } <br />
                                            <small>
                                              {
                                                <span title='Member ID'>{member.member_id}</span> ?? <span>No&nbsp;Member&nbsp;ID</span>
                                              }
                                              <span className='text-dark fw-bold mx-1'></span>
                                              {
                                                <span title='Referral Code'>{member.referral_code}</span> ?? 'No referral code'
                                              }
                                            </small>
                                          </div>
                                        </div>
                                      </div>
                                    </td>
                                    <td className='align-middle'>
                                      {member.slot_program} Program
                                    </td>
                                    <td className='align-middle'>
                                      {member.rank}
                                    </td>
                                    <td className='align-middle'>
                                      {member.email}
                                    </td>
                                    <td className='align-middle'>
                                      {member.telephone ?? <span>No&nbsp;telephone</span>}
                                    </td>
                                    <td className='align-middle'>
                                      {formatDate(member.created_at)}
                                    </td>
                                    <td className='align-middle'>
                                      <small className={member.payment_status === 'active' ? 'text-success' : 'text-danger'}>{member.payment_status === 'active' ? 'PAID' : 'UNPAID'}</small>
                                    </td>
                                    <td className='align-middle'>
                                      <div style={{ width: '100px' }} className='d-flex align-items-center'>
                                        <div>{member.verification_status}</div>
                                        <FontAwesomeIcon style={{ cursor: 'pointer' }} onClick={() => {
                                          let editstatus = query(`#editstatus${index}`)[0];
                                          editstatus.hidden = !editstatus.hidden;
                                        }} className='text-primary ms-2' title={`Update ${member.firstname ?? 'No name'}'s status`} icon={faEdit}></FontAwesomeIcon>
                                      </div>

                                      <form id={`editstatus${index}`} hidden={true} onSubmit={(e) => updateStatus(e, member, index)}>
                                        <div className="form-floating mt-2">
                                          <select className="form-control" id={`status${index}`} defaultValue={member.verification_status}>
                                            <option value="verified">verified</option>
                                            <option value="blocked">blocked</option>
                                            <option value="closed">closed</option>
                                          </select>
                                          <label htmlFor={`status${index}`} className=''>select&nbsp;status</label>
                                          <button type='submit' className='btn mt-2 w-100 btn-sm btn-primary'>update</button>
                                        </div>
                                      </form>

                                    </td>
                                    <td className='align-middle'>
                                      <div className='d-flex'>
                                        <button onClick={() => sendSingleNotice(member.id, member.firstname)} className='btn btn-sm btn-muted px-' title={`Send notice to ${member.firstname ?? 'No name'}`}><FontAwesomeIcon className='text-primary' icon={faInfoCircle}></FontAwesomeIcon></button>

                                        <Link to={`/users/downlines?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s downlines`}><FontAwesomeIcon className='text-primary' icon={faUsers}></FontAwesomeIcon></Link>

                                        <Link to={`/users/slots?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s referrals`}><FontAwesomeIcon className='text-primary' icon={faServer}></FontAwesomeIcon></Link>

                                        <Link to={`/users/benefits?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s benefits`}><FontAwesomeIcon className='text-primary' icon={faCrown}></FontAwesomeIcon></Link>

                                        <button onClick={() => editPrivilege(member.id, member.firstname, member.role, member.permissions)} className='btn btn-sm btn-muted' title={`Edit ${member.firstname ?? 'No name'}'s privileges`}><FontAwesomeIcon className='text-primary' icon={faShieldAlt}></FontAwesomeIcon></button>
                                      </div>
                                    </td>
                                  </tr>
                                ))
                                }
                              </tbody>

                            </table>
                          </div>

                          <div className='my-2 d-flex justify-content-between align-items-center'>

                            <button disabled={membersPage === 1} className='btn btn-sm btn-primary' onClick={() => { setMembersPage(membersPage - 1) }}>&laquo; Previous</button>

                            <span>Page {membersPage} / {Math.ceil(viewMembers.length / limit)}</span>

                            <button disabled={(((membersPage + 1) * limit) - limit) > viewMembers.length} className='btn btn-sm btn-primary px-4' onClick={() => { setMembersPage(membersPage + 1) }}>Next &raquo;</button>

                          </div>

                          <hr />
                          <button disabled={
                            members.every((member) => { return member.selected === false })
                          } className='btn btn-sm btn-primary' onClick={() => sendNoticeToMany('Members')}>
                            <FontAwesomeIcon className='text-light' icon={faInfoCircle}></FontAwesomeIcon> Notify Selected Members
                          </button>

                        </>
                      }
                    </div>

                  </>
                }

                {editPrivilegeModal()}
                {sendNoticeModal()}

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
