import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { alert, formatDate, formatMoney, request } from '../../../utils';
import { faCircle, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { useCallback } from 'react';

export const Payments = () => {
  const [payments, setPayments] = useState([])
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getpayments = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/payments', 'GET', null, headers);

      if (resp.status === true) {
        setPayments(resp.data)
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  const queryPayment = async (reference, invoicenumber) => {

    let headers = new Headers();
    headers.append('AuthToken', auth.token);

    let body = new FormData();
    body.append('reference', reference);
    body.append('invoicenumber', invoicenumber);

    try {
      let resp = await request('/api/admin/verify-payment', 'POST', body, headers);
      if (resp.status === true) {
        alert(resp.message, 'success');
        await getpayments();
      } else {
        alert(resp.message, 'error')
      }
    } catch (error) {
      alert('Query failed', 'error')
      console.log(error);
    }
  }

  useEffect(() => {
    getpayments();
    setPageIsLoading(false)
  }, [getpayments])

  const nopayments = () => {
    return <tr>
      <td colSpan={9} className='text-center'>No Payments.</td>
    </tr>
  }

  const renderpayments = () => {
    return payments.map((payment, index) => (
      <tr key={index}>
        <td>
          <span className={`text-muted ${payment.domain === 'live' && 'text-success'} ${payment.domain === 'test' && 'text-danger'}`} ><FontAwesomeIcon icon={faCircle}></FontAwesomeIcon></span>
          &nbsp;{index + 1}
        </td>
        <td>#{payment.invoice_number}</td>
        <td>{payment.email}</td>
        <td>{formatMoney(payment.amount_paid / 100, 0)}</td>
        <td>{payment.ip_address}</td>
        <td>{payment.payment_service}</td>
        <td>{payment.gateway_response}</td>
        <td>{payment.status}</td>
        <td>
          {
            payment.status === 'success'
              ? formatDate(payment.paid_at)
              : <button className='btn btn-sm pt-0 pb-1 btn-primary' onClick={() => queryPayment(payment.reference, payment.invoice_number)}>query</button>
          }
        </td>
      </tr>
    ))
  }

  return (
    <>
      <DashboardLayout page='/payments'>
        <section className='container my-3 px-3'>

          <div className='mb-3'>
            <h1 className='fw-normal fs-2'>Payments</h1>
            <hr className='my-0' />
          </div>
          <button id='addReferralLevelModalToggle' className="d-none btn btn-sm btn-outline-muted" data-bs-toggle="modal" data-bs-target="#addReferralLevelModal"></button>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div>
                  {
                    payments.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      There are no Referral Levels
                    </div>
                  }
                </div>

                {payments.length > 0 &&
                  <>
                    <div className="table-responsive">
                      <table className="table table-striped table-sm">
                        <thead>
                          <tr>
                            <th className='fw-500'>#</th>
                            <th className='fw-500'>Invoice&nbsp;Number</th>
                            <th className='fw-500'>Email</th>
                            <th className='fw-500'>Amount&nbsp;Paid</th>
                            <th className='fw-500'>IP&nbsp;Address</th>
                            <th className='fw-500'>Service</th>
                            <th className='fw-500'>Gateway&nbsp;Response</th>
                            <th className='fw-500'>Status</th>
                            <th className='fw-500'>Date&nbsp;Paid</th>
                          </tr>
                        </thead>
                        <tbody>
                          {payments.length > 0 ? renderpayments() : nopayments()}
                        </tbody>
                      </table>
                    </div>
                  </>
                }

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
