import { useState } from "react";
import { Facebook, Instagram, Linkedin, Twitter } from "react-feather";
import { Link } from "react-router-dom";
import { alert, query, request } from "../../../../utils";

export const ContactUsForm = () => {

  const [isSubmitting, setIsSubmitting] = useState(false);

  const submitMessage = async (e) => {
    e.preventDefault();
    let contactForm = query('#contactForm')[0];

    if (!contactForm.checkValidity()) {
      e.stopPropagation()
      contactForm.classList.add('was-validated')
    } else {
      setIsSubmitting(true)

      let subject = query('#contactSubject')[0];
      let name = query('#contactName')[0];
      let email = query('#contactEmail')[0];
      let message = query('#contactMessage')[0];

      let body = new FormData();
      body.append('subject', subject.value);
      body.append('name', name.value);
      body.append('email', email.value);
      body.append('message', message.value);

      let resp = await request('/api/contactus', 'POST', body);

      if (resp.status === true) {
        alert('Thank you for reaching out, we would get back to you as soon as possible', 'success', false);
        subject.value = ""; name.value = ""; email.value = ""; message.value = "";
      } else {
        alert('Message not submitted, please try again or send us a mail at support@sportsfansng.com', 'error', false);
      }

      setIsSubmitting(false)
    }

  }

  return (
    <div className="py-5 container">

      <div className="w-90 mx-auto">
        <p className='mb-5 lh-base text-center'>Check our <Link to='/faq' className="fw-500">FAQ</Link> page to find frequently asked questions about <br />Sports Fans Club</p>

        <div className='row'>

          <div className='d-lg-block d-md-none d-none col-lg-6 col-md-12'>
            <div className='px-3 py-lg-5'>

              <h2 className='mb-lg-4 bruno-ace text-primary d-md-none d-lg-inline-block'>
                WE WOULD LIKE <br />TO HEAR FROM YOU
              </h2>

              <div className='mb-3 mb-lg-4 lh-lg'>
                <p>
                  <span className="fw-500">EMAIL</span> <br />
                  <span>
                    <a className="text-dark" href="mailto:support@sportsfansng.com">
                      support&#64;sportsfansng.com
                    </a>
                  </span>
                </p>
              </div>

              <div className='mb-3 mb-lg-4 lh-lg'>
                <p>
                  <span className="fw-500">TELEPHONE</span> <br />
                  <span>
                    <a className="text-dark" href="tel:+2348023863399">+234 802 386 3399</a>,
                    &nbsp;
                    <a className="text-dark" href="tel:+2349093590559">+234 909 359 0559</a>
                  </span>
                </p>
              </div>

              <div className='mb-3 mb-lg-4 lh-lg'>
                <div>
                  <span className="fw-500">SOCIAL&nbsp;MEDIA</span> <br />

                  <div className='w-50 d-flex justify-content-start'>
                    <a href='https://facebook.com' className='text-dark me-lg-3 me-md-3'><Facebook /></a>
                    <a href='https://twitter.com' className='text-dark mx-lg-3 mx-md-3'><Twitter /></a>
                    <a href='https://instagram.com' className='text-dark mx-lg-3 mx-md-3'><Instagram /></a>
                    <a href='https://linkedin.com' className='text-dark mx-lg-3 mx-md-3'><Linkedin /></a>
                  </div>

                </div>
              </div>

              {/* <div className='mb-3 mb-lg-4'>
                <div>
                  <span className="fw-500">ADDRESS</span> <br />
                  <span>...</span>
                </div>
              </div> */}

            </div>
          </div>

          <div className='col-lg-6 col-md-12 col-12 px-0 px-md-3'>
            <div className='p-lg-5 p-1'>
              <form className='needs-validation' id='contactForm' onSubmit={submitMessage} noValidate={true} >

                <h2 className='mb-lg-4 mb-md-4 mb-4 text-primary d-lg-none d-md-inline-block text-center w-100'>
                  <span className="d-none d-md-inline-block d-lg-inline-block bruno-ace">WE WOULD LIKE TO HEAR <br />FROM YOU</span>
                  <span className="d-inline-block d-md-none d-lg-none bruno-ace">WE WOULD LIKE TO HEAR FROM YOU</span>
                </h2>

                <div className="form-floating mb-lg-4 mb-3">
                  <input type="text" disabled={isSubmitting} className="form-control rounded-0 border-grey" id="contactSubject" placeholder="Subject" required />
                  <label htmlFor="contactSubject">Subject</label>
                  <div className="invalid-feedback">Please enter a subject.</div>
                </div>

                <div className="form-floating mb-lg-4 mb-3">
                  <input type="text" disabled={isSubmitting} className="form-control rounded-0 border-grey" id="contactName" placeholder="Name" required />
                  <label htmlFor="contactName">Name</label>
                  <div className="invalid-feedback">Please enter your name.</div>
                </div>

                <div className="form-floating mb-lg-4 mb-3">
                  <input type="email" disabled={isSubmitting} className="form-control rounded-0 border-grey" id="contactEmail" placeholder="name@example.com" required />
                  <label htmlFor="contactEmail">Email address</label>
                  <div className="invalid-feedback">Please enter a email address.</div>
                </div>

                <div className="form-floating mb-lg-4 mb-3">
                  <textarea className="form-control rounded-0 border-grey" disabled={isSubmitting} placeholder="Leave a comment here" id="contactMessage" style={{ height: '100px' }} defaultValue={""} required />
                  <label htmlFor="contactMessage">Message</label>
                  <div className="invalid-feedback">Please enter a message.</div>
                </div>

                <div className=''>
                  <button className='btn btn-lg btn-primary px-5 form-control' disabled={isSubmitting} type="submit">
                    {isSubmitting && <span className="spinner-border spinner-border border-2 spinner-border-sm me-3" role="status" aria-hidden="true"></span>}
                    {isSubmitting === true ? 'Submitting' : 'Submit'}
                  </button>
                </div>

                <div className="w-100 d-lg-none d-md-inline-block d-inline-block py-3 d- flex justify -content-center">
                  <div className='mb-3 mb-lg-4 lh-lg'>
                    <div className='text-center mb-lg-0 mb-md-2 mb-3'>
                      <a className="text-dark" href="mailto:support@sportsfansng.com">
                        support&#64;sportsfansng.com
                      </a>,
                      &nbsp;
                      <br className="d-lg-none d-md-none d-block" />
                      <a className="text-dark" href="tel:+2348023863399">+234 802 386 3399</a>,
                      &nbsp;
                      <br className="d-lg-none d-md-none d-block" />
                      <a className="text-dark" href="tel:+2349093590559">+234 909 359 0559</a>
                    </div>
                    <div className='d-flex justify-content-center'>
                      <a href='https://facebook.com' className='text-dark me-lg-3 me-md-3 me-2'><Facebook /></a>
                      <a href='https://twitter.com' className='text-dark mx-lg-3 mx-md-3 mx-2'><Twitter /></a>
                      <a href='https://instagram.com' className='text-dark mx-lg-3 mx-md-3 mx-2'><Instagram /></a>
                      <a href='https://linkedin.com' className='text-dark mx-lg-3 mx-md-3 mx-2'><Linkedin /></a>
                    </div>
                  </div>
                </div>

              </form>
            </div>
          </div>

        </div>
      </div>

    </div >
  )
}