import { useState } from "react";
import { useHistory } from "react-router-dom";
import { alert, appUrl, query, request } from "../../../utils";
import { Footer, Header } from "../../../components";
import { Helmet } from "react-helmet";

export const ForgotPassword = () => {

  const [isSubmitting, setIsSubmitting] = useState(false)
  const [email, setEmail] = useState("")
  const [canVerify, setCanVerify] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [canReset, setCanReset] = useState(false)
  const history = useHistory();

  const submitForgotPassword = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsSubmitting(true)
    let forgotForm = query('#forgotForm')[0];

    if (!forgotForm.checkValidity()) {
      forgotForm.classList.add('was-validated')
    } else {
      try {
        let email = query('#forgotEmail')[0].value;
        let body = new FormData();
        body.append('email', email);
        setEmail(email)
        let resp = await request('/api/forgotpassword', 'POST', body);

        if (resp.status === true) {
          alert(resp.message, 'error');
          setCanVerify(true)
        } else {
          alert(`Verfication token was not sent to ${email}`, 'error');
        }
      } catch {
        alert('Network Error', 'error')
      }
    }
    setIsSubmitting(false)
  }

  const verifyToken = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    setIsSubmitting(true)
    let token = query('#verificationToken')[0].value;

    let body = new FormData();
    body.append('token', token);
    body.append('email', email);
    try {
      let resp = await request('/api/forgotpassword/verify', 'POST', body);

      if (resp.status === true) {
        // ResetPassword.state.email = state.email;
        alert(`${email} is verified successfully`, 'success');
        // history.push('/reset');
        query('#forgotEmail')[0].value = "";
        query('#verificationToken')[0].value = "";
        setCanReset(true)
      } else {
        alert(resp.message, 'error');
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
    setIsSubmitting(false)
  }

  const submitResetPassword = async (e) => {
    e.preventDefault();
    let resetForm = query('#resetForm')[0];

    if (!resetForm.checkValidity()) {
      e.stopPropagation()
      resetForm.classList.add('was-validated')
    } else {
      setIsSubmitting(true)

      let password = query('#resetPassword')[0].value;
      let cpassword = query('#resetConfirmPassword')[0].value;

      let body = new FormData();
      body.append('email', email);
      body.append('password', password);
      body.append('cpassword', cpassword);

      let resp = await request('/api/forgotpassword/reset', 'POST', body);

      if (resp.status === true) {
        alert(`Password reset successful`, 'success');
        setTimeout(() => {
          history.push('/login');
        }, 2000);
      } else {

        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')

      }
    }

    setIsSubmitting(false)
  }

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | Forgot Password</title>
        <link rel="canonical" href={`${appUrl}/forgot-password`} />
        <meta name="description" content='Enter to email address to recover your password' />
      </Helmet>

      <div className="bg-light w-100">
        <Header />
        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-5 col-12'>

            {canReset === false
              ?
              <div>

                <div className="text-center mb-lg-4">
                  {/* <div className='bruno-ace fw-light fs-1 d-lg-none'>Join Us</div> */}
                  <h1 className='bruno-ace text-primary fw-light d-none d-lg-inline-block'>Forgot Password</h1>
                  <div>
                    {canVerify === false ?
                      <span>Enter your email address</span>
                      :
                      <div>Enter the 5 digit token sent to your email address ({email})</div>
                    }
                  </div>
                </div>

                <form id='forgotForm' onSubmit={(e) => canVerify === false ? submitForgotPassword(e) : verifyToken(e)} noValidate={true} >

                  <div className="form-floating mb-4">
                    <input type="email" readOnly={canVerify} disabled={isSubmitting} className="form-control rounded-0" id="forgotEmail" placeholder="name@example.com" required />
                    <label htmlFor="forgotEmail">Email address</label>
                    <div className="invalid-feedback">Please enter an email address.</div>
                  </div>

                  {
                    canVerify === true &&
                    <div className="form-floating mb-4">
                      <input type="text" disabled={isSubmitting} className="form-control rounded-0" id="verificationToken" placeholder="******" pattern="[0-9]{5}" />
                      <label htmlFor="verificationToken">Verification Token</label>
                    </div>
                  }

                  <div className='pb-4'>

                    {
                      canVerify === false ?
                        <button className='btn btn-lg w-100 btn-primary px-5' disabled={isSubmitting} type="submit">
                          {
                            isSubmitting === true
                              ? <div>
                                <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                                <span className='ms-2 align-middle'>Submitting...</span>
                              </div>
                              : <span className='ms-2 align-middle'>Submit</span>}
                        </button>
                        :
                        <button className='btn btn-lg w-100 btn-primary px-5' disabled={isSubmitting} type="submit">
                          {
                            isSubmitting === true
                              ? <div>
                                <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                                <span className='ms-2 align-middle'>Verifying your account...</span>
                              </div>
                              : <span className='ms-2 align-middle'>Verify</span>}
                        </button>
                    }
                  </div>

                </form>
              </div>
              :
              <div>
                <div className='text-center mb-3'>
                  <div className='fw-light fs-1'>Reset Password</div>
                  <span>Choose a new password</span>
                </div>

                <form id='resetForm' onSubmit={(e) => submitResetPassword(e)} noValidate={true} >
                  <div className="form-floating mb-1 clearfix">
                    <input type={showPassword === true ? "text" : "password"} disabled={isSubmitting} className="form-control rounded-0" id="resetPassword" placeholder='******' required defaultValue="" />
                    <label htmlFor="resetPassword">Password<span className='text-danger ms-1'>*</span></label>
                    <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                      setShowPassword(!showPassword)
                    }
                    }>{showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                    <div className="invalid-feedback">Please enter a secured password.</div>
                  </div>

                  <div className="form-floating mb-1 clearfix">
                    <input type={showConfirmPassword === true ? "text" : "password"} disabled={isSubmitting} className="form-control rounded-0" id="resetConfirmPassword" placeholder='******' required defaultValue="" />
                    <label htmlFor="resetConfirmPassword">Confirm Password<span className='text-danger ms-1'>*</span></label>
                    <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                      setShowConfirmPassword(!showConfirmPassword)
                    }
                    }>{showConfirmPassword === true ? 'Hide Password' : 'Show Password'}</div>
                    <div className="invalid-feedback">Please enter a secured password.</div>
                  </div>

                  <div className='pt-4'>

                    <button className='btn btn-lg w-100 btn-primary px-5' disabled={isSubmitting} type="submit">
                      {
                        isSubmitting === true
                          ? <div>
                            <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                            <span className='ms-2 align-middle'>Resetting your password...</span>
                          </div>
                          : <span className='ms-2 align-middle'>Reset</span>}
                    </button>

                  </div>

                </form>

              </div>
            }

          </div>

        </section>

        <Footer />
      </div>
    </>
  )
}