import { Footer, Header } from "../../../components";
import { useState } from "react";
import { alert, appUrl, query } from "../../../utils";
import { Link, useHistory } from "react-router-dom";
import { useAuthStore } from "../../../store/auth";
import { useUserStore } from "../../../store/user";
import { Helmet } from "react-helmet";

export const Login = () => {

  const history = useHistory();
  const { login } = useAuthStore()
  const { getProfile } = useUserStore()
  const [state, setState] = useState({
    isSubmitting: false,
    showPassword: false,
    showConfirmPassword: false,
    slots: [],
    queryMap: {}
  });

  const handleLogin = async (event) => {
    try {
      event.preventDefault();

      setState({ ...state, isSubmitting: true })
      let email = query('#loginEmail')[0].value;
      let password = query('#loginPassword')[0].value;

      let resp = await login(email, password);

      const auth = useAuthStore.getState().auth;
      if (auth.authenticated === true) {
        await getProfile(auth.token)
        alert(`Welcome back ${email}`, 'success');

        history.push('/dashboard');

      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp?.data?.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;

        alert(errmsg, 'error')
        setState({ ...state, isSubmitting: false })
      }

    } catch (error) {
      alert('Network Error', 'error')
      setState({ ...state, isSubmitting: false })
    }

  }

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | Login</title>
        <link rel="canonical" href={`${appUrl}/login`} />
        <meta name="description" content='Login to continue' />
      </Helmet>

      <div className="bg-light w-100">
        <Header />

        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-6 col-md-10 col-12'>

            <div className="text-center mb-lg-4">
              <div className='bruno-ace text-primary fw-light fs-1 d-lg-none'>Login</div>
              <h1 className='bruno-ace text-primary fw-light d-none d-lg-inline-block'>Login</h1>
              <div>Don't have an account? <Link className='text-primary' to='/register'>Create an account</Link></div>
            </div>

            <form id='loginForm' onSubmit={handleLogin} noValidate={true} >

              <div className="form-floating mb-4">
                <input type="email" disabled={state.isSubmitting} className="form-control rounded-0" id="loginEmail" placeholder="name@example.com" />
                <label htmlFor="loginEmail">Email address</label>
              </div>

              <div className="form-floating mb-4 clearfix">
                <input type={state.showPassword === true ? "text" : "password"} autoComplete='false' disabled={state.isSubmitting} className="form-control rounded-0" id="loginPassword" placeholder='******' />
                <label htmlFor="loginPassword">Password</label>
                <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                  setState({ ...state, showPassword: !state.showPassword });
                }
                }>{state.showPassword === true ? 'Hide Password' : 'Show Password'}</div>
              </div>

              <button className='btn btn-lg w-100 btn-primary px-5' disabled={state.isSubmitting} type="submit">
                {
                  state.isSubmitting === true
                    ? <div>
                      <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                      <span className='ms-2 align-middle'>Logging you in...</span>
                    </div>
                    : <span className='ms-2 align-middle'>Login</span>}
              </button>

              <div className="my-4 clearfix">
                <Link className='text-primary float-end' to='/forgot-password'>Forgot password</Link>
              </div>

            </form>

          </div >

        </section >

        <Footer />
      </div>
    </>
  )
}