import { faAngleDown, faAngleRight, faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { baseUrl, parseUrlQuery, request } from '../../../utils';
import defaultProfilePicture from '../../../assets/image/default_profile_picture.png';
import logo from '../../../assets/image/sportsfans_logo.png';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { useHistory } from 'react-router-dom';
import { useCallback } from 'react';

export const UserDownlines = () => {
  const [isOrg, setIsOrg] = useState(true)
  const [downlines, setDownlines] = useState([])
  const [curDownline, setCurDownline] = useState([])
  const [curMember, setCurMember] = useState({})
  const [downlineStack] = useState([])
  const [curLevel, setCurLevel] = useState('Manager')
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()
  const history = useHistory()

  const getDownlines = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let query = parseUrlQuery();
      console.log(query.email)
      let resp = null;
      if (query.email === undefined) {
        setIsOrg(true)
        resp = await request('/api/admin/org-downlines', 'GET', null, headers);
      } else {
        setIsOrg(false)
        resp = await request('/api/admin/user-downlines?email=' + query.email, 'GET', null, headers);
      }
      if (resp.status === true) {
        if (isOrg === true) {
          setDownlines(resp.data)
          setCurDownline(resp.data)
          setCurMember({
            firstname: "SPORTS FANS CLUB",
            lastname: "",
            telephone: "No number",
            downline: resp.data,
            profile_picture: logo,
          })
        } else {
          setDownlines(resp.data.downlines)
          setCurDownline(resp.data.downlines)
          setCurMember({
            firstname: resp.data.user.firstname,
            lastname: resp.data.user.lastname,
            telephone: resp.data.user.telephone,
            profile_picture: resp.data.user.profile_picture,
            downline: resp.data.downlines,
          })
        }
      }

    } catch (error) {
      console.log(error);
    }
  }, [auth.token, isOrg])

  useEffect(() => {
    getDownlines();
    setPageIsLoading(false)
  }, [getDownlines])

  const formatLevel = (level) => {
    switch (level) {
      case 1:
        return 'Coach';
      case 2:
        return 'Players';
      case 3:
        return 'Supporters';
      case 4:
        return 'Stadium';
      case 5:
        return 'Fans';
      case 6:
        return 'Trophy';
      default:
        return 'Manager';
    }
  }

  const setCurrentDownline = (member) => {
    if (curMember != null) downlineStack.push(curMember);
    setCurDownline(member.downline)
    setCurMember(member)
    setCurLevel(formatLevel(member.level))
  }

  const setPrevDownline = () => {
    if (downlineStack.length === 0) {
      setCurDownline(downlines)
      setCurMember(null)
      setCurLevel('Manager')
      return;
    }

    let member = downlineStack.pop();
    setCurDownline(member.downline)
    setCurMember(member)
    setCurLevel(formatLevel(member.level))
  }

  return (
    <>
      <DashboardLayout page='/alldownlines'>
        <section className='my-3 px-3 text-muted'>

          <div className='mb-3 text-dark'>

            <h1 className='fw-normal fs-2'>Downlines</h1>
            <hr className='my-0' />
            <small>Current Level: {isOrg === true ? 'Organization' : curLevel}</small>
            {
              isOrg === false &&
              <>
                <br />
                <button className='btn btn-sm btn-muted' onClick={() => { history.goBack() }}>
                  <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
                </button>
              </>
            }
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                {/* Downlines */}
                <div className='my-3'>
                  {
                    // downlines.length === 0 ? 'Your downline is empty' :
                    <div>
                      {
                        <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                          <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={downlineStack.length > 0 ? setPrevDownline : () => { }} >
                            <div className='d-flex align-items-center'>
                              {
                                downlineStack.length > 0 &&
                                // return to previous
                                <div className='me-3'>
                                  <FontAwesomeIcon size='1x' icon={faArrowLeft}></FontAwesomeIcon>
                                </div>
                              }

                              {/* the profile picture */}
                              <div className='me-lg-4 me-3'>
                                {
                                  isOrg === true
                                    && downlineStack.length === 0 ?
                                    // curMember === null ?
                                    <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={logo} alt="" />
                                    :
                                    <img id='profilePicturePreview' className='rounded-circle' width='55px' height='55px' src={curMember.profile_picture != null ? `${baseUrl}/${curMember.profile_picture}` : defaultProfilePicture} alt="" />
                                }
                              </div>

                              {/* the names */}
                              <div>
                                <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                  {
                                    <>
                                      <div>
                                        {curMember.firstname === null && curMember.lastname === null ? 'No name' : `${curMember.firstname} ${curMember.lastname}`}
                                      </div>
                                      <div>{curMember.telephone ?? 'No number'}</div>
                                    </>
                                  }
                                </div>
                              </div>
                            </div>

                            {/* clickable indication */}
                            <div>
                              <FontAwesomeIcon size='2x' icon={faAngleDown}></FontAwesomeIcon>
                            </div>

                          </div>
                        </div>
                      }
                      <div className='border-start ms-4 border-3 my-3 pt-2 pb-1 border-muted'>
                        {
                          curDownline.length > 0 ?
                            curDownline.map((member, index) => (
                              <div key={index} className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                                <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={member.downline != null && curLevel !== 'Trophy' ? (() => setCurrentDownline(member)) : () => { }} >
                                  <div className='d-flex'>
                                    {/* the profile picture */}
                                    <div className='me-lg-4 me-3'>
                                      <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                    </div>

                                    {/* the names */}
                                    <div>
                                      <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                        <div>
                                          {member.firstname === null && member.lastname === null ? 'No name' : `${member.firstname} ${member.lastname}`}
                                        </div>
                                        <div>{member.telephone || 'No number'}</div>
                                      </div>
                                    </div>
                                  </div>

                                  {
                                    member.downline.length > 0 &&
                                    // clickable indication
                                    <div>
                                      <FontAwesomeIcon size='2x' icon={faAngleRight}></FontAwesomeIcon>
                                    </div>
                                  }

                                </div>
                              </div>
                            ))
                            :
                            <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                              There is no downline for this member.
                            </div>
                        }
                      </div>
                    </div>
                  }
                </div>
              </div>
          }
        </section>
      </DashboardLayout>
    </>
  );
}