import { baseUrl } from "./constants";

export const request = async (uri, method = 'GET', body = {}, headers = {}, external = false) => {
  try {
    let resp = await fetch(
      // let resp = await fetch(
      external === false ? baseUrl + uri : uri,
      // Request with GET/HEAD method cannot have body.
      method === 'GET' || method === 'HEAD' ? {
        mode: external === false ? 'cors' : 'no-cors',
        method: method,
        headers: headers,
      } : {
        mode: external === false ? 'cors' : 'no-cors',
        method: method,
        headers: headers,
        body: body
      }
    );
    return resp.json();
  } catch (error) {
    throw new Error(error.toString());
  }
}
