import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState, useEffect } from 'react';
import { baseUrl, parseUrlQuery, request } from '../../../utils';
import defaultProfilePicture from '../../../assets/image/default_profile_picture.png';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';

export const UserProfile = () => {

  const [profile, setProfile] = useState({})
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()
  const history = useHistory()

  const getProfile = useCallback(async (email) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('email', email)
      let resp = await request('/api/admin/users/profile', 'POST', body, headers);

      if (resp.status === true) {
        setProfile(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  useEffect(() => {
    let query = parseUrlQuery();
    getProfile(query.u ?? '');
    setPageIsLoading(false)
  }, [getProfile])

  return (
    <>
      <DashboardLayout page='/users'>

        <section className='container my-3 px-3 text-muted'>

          <div className='d-flex justify-content-between align-items-center text-dark'>
            <h1>
              User Profile
              <small className='fs-6 d-block fw-light'>{profile.role} <b></b> {profile.verification_status}</small>
            </h1>
            <div className='my-1'>
              <img id='profilePicturePreview' className='rounded-circle' width='60px' height='60px' src={profile.profile_picture != null ? `${baseUrl}/${profile.profile_picture}` : defaultProfilePicture} alt="" />
            </div>
          </div>

          <hr className='mt-0' />
          <button className='btn btn-sm btn-muted' onClick={() => { history.goBack() }}>
            <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
          </button>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                <div className='mb-3 rounded shadow-sm px-lg-4 px-3 py-3'>

                  <div className='mb-3'>
                    <h3>Bio Info</h3>

                    <div className='row'>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <h5 className='mt-4'>Personal Info</h5>
                        <div className='mb-1'><small><b>MEMBER ID:</b></small> {profile.member_id}</div>
                        <div className='mb-1'><small><b>FIRSTNAME:</b></small> {profile.firstname}</div>
                        <div className='mb-1'><small><b>LASTNAME:</b></small> {profile.lastname}</div>
                        <div className='mb-1'><small><b>MIDDLENAME:</b></small> {profile.middlename}</div>
                        <div className='mb-1'><small><b>EMAIL:</b></small> {profile.email}</div>
                        <div className='mb-1'><small><b>TELEPHONE:</b></small> {profile.telephone}</div>
                        <div className='mb-1'><small><b>OCCUPATION:</b></small> {profile.occupation}</div>
                        <div className='mb-1'><small><b>NATIONALITY:</b></small> {profile.nationality}</div>
                        <div className='mb-1'><small><b>RESIDENTIAL ADDRESS:</b></small> {profile.residential_address}</div>
                      </div>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <h5 className='mt-4'>Member Info</h5>
                        <div className='mb-1'><small><b>REFERRAL CODE:</b></small> {profile.referral_code}</div>
                        <div className='mb-1'><small><b>PROGRAM:</b></small> {profile.slot_program} Program</div>
                        <div className='mb-1'><small><b>RANK:</b></small> {profile.rank}</div>

                        <h5 className='mt-4'>Next of Kin Info</h5>
                        <div className='mb-1'><small><b>NEXT OF KIN FULLNAME:</b></small> {profile.nextofkin_name}</div>
                        <div className='mb-1'><small><b>NEXT OF KIN TELEPHONE:</b></small> {profile.nextofkin_telephone}</div>
                        <div className='mb-1'><small><b>NEXT OF KIN RESIDENTIAL ADDRESS:</b></small> {profile.nextofkin_residential_address}</div>
                      </div>
                    </div>

                  </div>

                  <hr />

                  <div className='mb-3'>
                    <h3>FAVOURITE SPORT &amp; TEAMS</h3>
                    <h5 className='mt-4'>FAVOURITE SPORT: {profile.favourite_sport}</h5>

                    <div className='row'>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <div className='mb-1'><small><b>LOCAL TEAM:</b></small> {profile.favourite_team_local}</div>
                        <div className='mb-1'><small><b>FOREIGN TEAM:</b></small> {profile.favourite_team_foreign}</div>
                        <div className='mb-1'><small><b>INTERNATIONAL TEAM:</b></small> {profile.favourite_team_international}</div>
                      </div>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <div className='mb-1'><small><b>CONTINENTAL TEAM:</b></small> {profile.favourite_team_continental}</div>
                        <div className='mb-1'><small><b>WORLD CUP TEAM:</b></small> {profile.favourite_team_worldcup}</div>
                        <div className='mb-1'><small><b>OLYMPIC TEAM:</b></small> {profile.favourite_team_olympic}</div>
                      </div>
                    </div>

                  </div>

                  <hr />

                  <div className='mb-3'>
                    <h3>BANK DETAILS</h3>

                    <div className='row'>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <div className='mb-1'><small><b>BANK:</b></small> {profile.bankname}</div>
                        <div className='mb-1'><small><b>ACCOUNT NAME:</b></small> {profile.accountname}</div>
                        <div className='mb-1'><small><b>ACCOUNT NUMBER:</b></small> {profile.accountnumber}</div>
                      </div>
                    </div>

                  </div>

                </div>

              </div>
          }
        </section>

      </DashboardLayout>
    </>
  );
}