import { request } from "../utils";
import create from 'zustand'
import { devtools } from 'zustand/middleware'

export const useAuthStore = create(
  devtools(
    (set, get) => ({
      auth: {
        token: '',
        email: '',
        role: '',
        permissions: [],
        authenticated: false,
      },

      boot: async () => {
        console.log('auth booting...')

        new Promise((resolve, reject) => {
          let auth = JSON.parse(localStorage.getItem('Auth')) ?? {
            token: '',
            email: '',
            role: 'guest',
            permissions: [],
            authenticated: false,
          };
          resolve(auth);
        })
          .then(auth => {
            set({ auth: { ...auth } })
          })
      },

      login: async (email, password) => {
        try {
          console.log('inside login')
          let body = new FormData();
          body.append('email', email);
          body.append('password', password);

          let resp = await request('/api/login', 'POST', body);

          if (resp.status === true) {
            let data = resp.data;

            localStorage.setItem('Auth', JSON.stringify({
              token: data.token,
              email: email,
              role: data.role,
              permissions: data.permissions ?? [],
              authenticated: true
            }));

            set({
              auth: {
                token: data.token,
                email: email,
                role: data.role,
                permissions: data.permissions ?? [],
                authenticated: true
              }
            })

          }

          console.log('done login')
          return resp;
        } catch (error) {
          throw error;
        }
      },

      logout: () => {
        localStorage.clear();
        set({
          auth: {
            token: '',
            email: '',
            role: '',
            permissions: [],
            authenticated: false,
          }
        })
      }

    })
  )
)
