import { faFacebookF, faTwitter, faWhatsapp } from '@fortawesome/free-brands-svg-icons';
import { faAngleDown, faAngleRight, faArrowLeft, faCopy, faPaperPlane } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { alert, appUrl, baseUrl, query, request } from '../../../utils';
import defaultProfilePicture from '../../../assets/image/default_profile_picture.png';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { useUserStore } from '../../../store/user';
import { useCallback } from 'react';

export const MyDownlines = () => {

  const [myDownlines, setMyDownlines] = useState([])
  const [curDownline, setCurDownline] = useState([])
  const [curMember, setCurMember] = useState(null)
  const [downlineStack] = useState([])
  const [curLevel, setCurLevel] = useState('Manager')
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore();
  const { user } = useUserStore()

  const getMyDownlines = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let resp = await request('/api/get-downlines', 'GET', null, headers);
      let mydownlines = resp.status === true ? resp.data : [];
      setMyDownlines(mydownlines);
      setCurDownline(mydownlines);
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  const sendEmailInvite = async (e) => {
    e.preventDefault();

    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let friendEmail = query('#inviteEmail')[0].value;
      let body = new FormData();
      body.append('friendEmail', friendEmail);

      let resp = await request('/api/sendinvite', 'POST', body, headers);
      if (resp.status === true) alert(`Email invite sent successfully to ${friendEmail}.`, 'success'); else alert('Email invite was not sent', 'error');
    } catch (error) {
      console.log(error);
    }

  }

  useEffect(() => {
    getMyDownlines()
    setPageIsLoading(false)
  }, [getMyDownlines])

  const formatLevel = (level) => {
    switch (level) {
      case 1:
        return 'Coach';
      case 2:
        return 'Players';
      case 3:
        return 'Supporters';
      case 4:
        return 'Stadium';
      case 5:
        return 'Fans';
      case 6:
        return 'Trophy';
      default:
        return 'Manager';
    }
  }

  const setCurrentDownline = (member) => {
    if (curMember != null) downlineStack.push(curMember);
    setCurDownline(member.downline);
    setCurMember(member);
    setCurLevel(formatLevel(member.level))
  }

  const setPrevDownline = () => {

    if (downlineStack.length === 0) {
      setCurDownline(myDownlines);
      setCurMember(null);
      setCurLevel('Manager');
      return;
    }

    let member = downlineStack.pop();
    setCurDownline(member.downline);
    setCurMember(member)
    setCurLevel(formatLevel(member.level))
  }

  const copyLink = () => {
    var copyText = query("#referralLink")[0];

    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /* For mobile devices */

    /* Copy the text inside the text field */
    document.execCommand("copy");

    /* Alert the copied text */
    alert("Copied to clipboard", 'success');
  }

  const copyCode = () => {
    var copyText = query("#referralCode")[0];

    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /* For mobile devices */

    /* Copy the text inside the text field */
    document.execCommand("copy");

    /* Alert the copied text */
    alert("Copied to clipboard", 'success');
  }


  return (
    <>
      <DashboardLayout page='/downlines'>
        <section className='container my-3 px-3 text-muted'>

          <div className='mb-3 text-dark'>

            <h1 className='fw-normal fs-2'>Your Downlines</h1>
            <hr className='my-0' />
            <small>Current Level: {curLevel}</small>

          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                {/* Downlines */}
                <div className='my-3'>
                  {
                    myDownlines.length === 0 ?
                      <div>
                        <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                          <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} >
                            <div className='d-flex'>
                              {/* the profile picture */}
                              <div className='me-lg-4 me-3'>
                                <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={user.profile_picture != null ? `${baseUrl}/${user.profile_picture}` : defaultProfilePicture} alt="" />
                              </div>

                              {/* the names */}
                              <div>
                                <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                  <div>
                                    {user.firstname === null && user.lastname === null ? 'No name' : `${user.firstname} ${user.lastname}`}
                                  </div>
                                  <div>{user.telephone || 'No number'}</div>
                                </div>
                              </div>
                            </div>

                          </div>
                        </div>
                        <div className='border-start ms-4 border-3 my-3 pt-2 pb-1 border-muted'>
                          <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                            Your downline is empty
                          </div>
                        </div>
                      </div>
                      :
                      <div>
                        {
                          <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                            <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={setPrevDownline} >
                              <div className='d-flex align-items-center'>
                                {
                                  curMember != null &&
                                  // return to previous
                                  <div className='me-3'>
                                    <FontAwesomeIcon size='1x' icon={faArrowLeft}></FontAwesomeIcon>
                                  </div>
                                }

                                {/* the profile picture */}
                                <div className='me-lg-4 me-3'>
                                  {
                                    curMember === null ?
                                      <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={user.profile_picture != null ? `${baseUrl}/${user.profile_picture}` : defaultProfilePicture} alt="" />
                                      :
                                      <img id='profilePicturePreview' className='rounded-circle' width='55px' height='55px' src={curMember.profile_picture != null ? `${baseUrl}/${curMember.profile_picture}` : defaultProfilePicture} alt="" />
                                  }
                                </div>

                                {/* the names */}
                                <div>
                                  <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                    {
                                      curMember === null ?
                                        <>
                                          <div>
                                            {user.firstname === null && user.lastname === null ? 'No name' : `${user.firstname} ${user.lastname}`}
                                          </div>
                                          <div>{user.telephone || 'No number'}</div>
                                        </>
                                        :
                                        <>
                                          <div>
                                            {curMember.firstname === null && curMember.lastname === null ? 'No name' : `${curMember.firstname} ${curMember.lastname}`}
                                          </div>
                                          <div>{curMember.telephone || 'No number'}</div>
                                        </>
                                    }
                                  </div>
                                </div>
                              </div>

                              {/* clickable indication */}
                              <div>
                                <FontAwesomeIcon size='2x' icon={faAngleDown}></FontAwesomeIcon>
                              </div>

                            </div>
                          </div>
                        }
                        <div className='border-start ms-4 border-3 my-3 pt-2 pb-1 border-muted'>
                          {
                            curDownline.length > 0 ?
                              curDownline.map((member, index) => (
                                <div key={index} className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                                  <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={member.downline != null && curLevel !== 'Trophy' ? (() => setCurrentDownline(member)) : () => { }} >
                                    <div className='d-flex'>
                                      {/* the profile picture */}
                                      <div className='me-lg-4 me-3'>
                                        <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                      </div>

                                      {/* the names */}
                                      <div>
                                        <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                          <div>
                                            {member.firstname === null && member.lastname === null ? 'No name' : `${member.firstname} ${member.lastname}`}
                                          </div>
                                          <div>{member.telephone || 'No number'}</div>
                                        </div>
                                      </div>
                                    </div>

                                    {
                                      member.downline.length > 0 &&
                                      // clickable indication
                                      <div>
                                        <FontAwesomeIcon size='2x' icon={faAngleRight}></FontAwesomeIcon>
                                      </div>
                                    }

                                  </div>
                                </div>
                              ))
                              :
                              <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                                There is no downline for this member.
                              </div>
                          }
                        </div>
                      </div>
                  }
                </div>

                {/* Email Share */}
                <div className='py-3 mb-3'>
                  <h4>
                    {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                    <a name="invite-friends">
                      Invite your friends
                    </a>
                  </h4>
                  <small>Enter your friend's email address and send them invitations to join Sports Fans Club!</small>

                  <form id='sendEmailInviteForm' className='mt-lg-3 mt-2 position-relative d-flex align-items-center' onSubmit={sendEmailInvite} noValidate={true} >

                    <input style={{ borderRadius: '50px', paddingRight: '60px' }} placeholder='Email address' type="email" className="form-control form-control-lg fs-6 py-3 pl-3" id="inviteEmail" defaultValue='' required />
                    <div className="invalid-feedback">Please enter your friend's email address.</div>

                    <div className='position-absolute end-0 align-middle mt-2 me-1'>
                      <button type='submit' className="btn mb-2 d-flex align-items-center justify-content-center bg-primary text-light rounded-circle fs-3 p-4" style={{ width: '5px', height: '5px' }}>
                        <FontAwesomeIcon icon={faPaperPlane}></FontAwesomeIcon>
                      </button>
                    </div>

                  </form>
                </div>

                {/* Social Share */}
                <div className='py-3 mb-3'>
                  <h4>Share the referral link</h4>
                  <small>You can also share your referral link or referral code by copying and sending it or sharing it on your social media.</small>

                  {/* Copy Code */}
                  <div className='mt-lg-3 mt-1 w-100 position-relative d-flex align-items-center'>

                    <input id='referralCode' style={{ borderRadius: '50px', paddingRight: '60px' }} type="text" readOnly={true} className="form-control form-control-lg fs-6 py-3 pl-3 bg-light border-0" defaultValue={user.referral_code} />

                    <div className='bg-light rounded position-absolute end-0 align-middle mt-2 me-2'>
                      <button className="btn btn-muted py-0 px-0 pe-2 mb-2 text-primary" onClick={copyCode}>
                        <span className='d-none d-md-inline-block'>Copy code</span>
                        <span className='d-inline-block d-md-none'>
                          <FontAwesomeIcon icon={faCopy}></FontAwesomeIcon>
                        </span>
                      </button>
                    </div>

                  </div>

                  <div className='mt-lg-3 mt-1 w-100 position-relative d-flex align-items-center'>

                    <input id='referralLink' style={{ borderRadius: '50px', paddingRight: '60px' }} type="text" readOnly={true} className="form-control form-control-lg fs-6 py-3 pl-3 bg-light border-0" defaultValue={`${appUrl}/register?referral=${user.referral_code}`} />

                    <div className='bg-light rounded position-absolute end-0 align-middle mt-2 me-2'>
                      <button className="btn btn-muted py-0 px-0 pe-2 mb-2 text-primary" onClick={copyLink}>
                        <span className='d-none d-md-inline-block'>Copy link</span>
                        <span className='d-inline-block d-md-none'>
                          <FontAwesomeIcon icon={faCopy}></FontAwesomeIcon>
                        </span>
                      </button>
                    </div>

                  </div>

                  <div className='my-4 row d-flex justify-content-center'>

                    <div className='col-4'>
                      <a target="blank" href="https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fsportsfansng.com%2Fregister%3Freferral%3D12345678&amp;src=sdkpreparse" className='btn m-0 p-0 rounded d-flex justify-content-center align-items-center'>
                        <div type='submit' className="me-lg-2 d-flex align-items-center justify-content-center bg-light text-primary rounded-circle fs-3 p-4" style={{ width: '5px', height: '5px' }}>
                          <FontAwesomeIcon icon={faFacebookF}></FontAwesomeIcon>
                        </div>
                        <span className='d-lg-block d-none'>Share on Facebook</span>
                      </a>
                    </div>

                    <div className='col-4'>
                      <a target="blank" href={`https://twitter.com/intent/tweet?text=Become%20a%20member%20of%20Sports%20Fans%20Club%20by%20clicking%20on%20my%20referral%20link%20below.%20https%3A%2F%2Fsportsfansng.com%2Fregister%3Freferral%3D${user.referral_code}`} className='btn m-0 p-0 rounded d-flex justify-content-center align-items-center'>
                        <div type='submit' className="me-lg-2 d-flex align-items-center justify-content-center bg-light text-primary rounded-circle fs-3 p-4" style={{ width: '5px', height: '5px' }}>
                          <FontAwesomeIcon icon={faTwitter}></FontAwesomeIcon>
                        </div>
                        <span className='d-lg-block d-none'>Share on Twitter</span>
                      </a>
                    </div>

                    <div className='col-4'>
                      <a target="blank" href={`https://api.whatsapp.com/send?text=Become%20a%20member%20of%20Sports%20Fans%20Club%20by%20clicking%20on%20my%20referral%20link%20below.%20https%3A%2F%2Fsportsfansng.com%2Fregister%3Freferral%3D${user.referral_code}`} className='btn m-0 p-0 rounded d-flex justify-content-center align-items-center'>
                        <div type='submit' className="me-lg-2 d-flex align-items-center justify-content-center bg-light text-primary rounded-circle fs-3 p-4" style={{ width: '5px', height: '5px' }}>
                          <FontAwesomeIcon icon={faWhatsapp}></FontAwesomeIcon>
                        </div>
                        <span className='d-lg-block d-none'>Share on WhatsApp</span>
                      </a>
                    </div>

                  </div>

                </div>

              </div>
          }
        </section>
      </DashboardLayout>
    </>
  );
}
