import React, { useEffect, useState } from 'react';
import { faAngleUp, faGlobeAfrica, faPencilAlt, faPlus, faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { alert, query, request } from '../../../utils';
import { useAuthStore } from '../../../store/auth';
import { DashboardLayout } from '../../../components';
import { useCallback } from 'react';

export const Competitions = () => {
  const [competitions, setCompetitions] = useState([])
  const [isEditing, setIsEditing] = useState(false)
  const [isSubmitting] = useState(false)
  const [currentCompetition, setCurrentCompetition] = useState({})
  const [currentCompetitionId, setCurrentCompetitionId] = useState(0)
  const [currentCompetitionQualifiedTeam, setCurrentCompetitionQualifiedTeam] = useState([])
  const [availableSports] = useState([
    "Football", "American Football", "Baseball", "Basketball", "Car Racing", "Boxing", "Swimming", "Tennis", "Golf", "Racing", "Hockey"
  ])
  const [slotPackages, setSlotPackages] = useState([])
  const [selectedSlot, setSelectedSlot] = useState("1")
  const [countries, setCountries] = useState([])
  const [filteredCompetitions, setFilteredCompetitions] = useState([])
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getSlotPackages = useCallback(async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/slots', 'GET', null, headers);

      if (resp.status === true) {
        setSlotPackages(resp.data)
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  const getCountries = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/getcountries', 'GET', null, headers);

      if (resp.status === true) {
        setCountries(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  const getCompetitions = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/competitions', "GET", null, headers)

      if (resp.status === true) {
        setCompetitions(resp.data)
        setFilteredCompetitions(resp.data)
        setCurrentCompetitions(resp.data[0]);
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
  }, [auth.token])

  useEffect(() => {
    getCompetitions()
    getSlotPackages()
    getCountries()

    setPageIsLoading(false)
  }, [getCompetitions, getCountries, getSlotPackages])

  const filter = () => {
    let region = query('#region')[0].value;
    let sport = query('#sport')[0].value ?? "Football";
    let newCompetitions = competitions
      .filter((competition) => { return competition.sport === sport; })
      .filter((competition) => { return region === "" ? true : competition.region === region; })
    setFilteredCompetitions(competitions)
    setCurrentCompetitions(newCompetitions[0]);
  }

  const setCurrentCompetitions = (competition) => {
    if (competition === undefined) {
      setCurrentCompetition({})
      setCurrentCompetitionId(0)
      setCurrentCompetitionQualifiedTeam([])
    }
    else {
      setCurrentCompetition(competition)
      setCurrentCompetitionId(competition.id)
      setCurrentCompetitionQualifiedTeam(JSON.parse(competition.qualified_teams))
    }
  }

  const renderCompetitions = () => {
    let competitions = filteredCompetitions

    if (competitions.length === 0)
      return <div>
        No Competitions.
      </div>

    return <ul className="list-group">
      {
        competitions.map((competition, index) => (
          <li key={index} style={{ cursor: 'pointer' }} className={`list-group-item ${competition.id === currentCompetitionId && 'bg-primary text-light'}`} onClick={() => setCurrentCompetitions(competition)}>
            <div className='fw-500'>{competition.region}</div>
            <small>{competition.competition}</small>
          </li>
        ))
      }
    </ul>
  }

  const renderCompetitionQualifiedTeams = () => {

    if (currentCompetitionQualifiedTeam.length === 0)
      return <div>
        No Qualified Teams.
      </div>

    let list = [];
    slotPackages.forEach((slot) => { list.push(slot.program); })

    return <>

      <div className="p-2 rounded border mb-3">
        <h6>{currentCompetition.competition}</h6>
        <small>
          <FontAwesomeIcon icon={faGlobeAfrica}></FontAwesomeIcon> {currentCompetition.region}
          <span className='mx-1 fw-bold'></span>
          <b className='fw-500'>
            {currentCompetition.sport}
          </b>
          <span className='mx-1 fw-bold'></span>
          <b className='fw-500'>
            {list[currentCompetition.slotpackage - 1]} Slot Package
          </b>
        </small>
      </div>

      <ul className="list-group">
        {
          currentCompetitionQualifiedTeam.map((team, index) => (
            <li key={index} className="list-group-item">
              <div className="d-flex justify-content-between align-items-start">
                <div className="fw-500">{index + 1}.<span className='ms-lg-4'>{team}</span></div>
              </div>
            </li>
          ))
        }
      </ul>
    </>
  }

  const addCompetitionAndQualifiedTeamsModal = () => {
    return (
      <div>
        <div className="modal fade" id="addCompetitionAndQualifiedTeamsModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="addCompetitionAndQualifiedTeamsModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addCompetitionAndQualifiedTeamsModalLabel">{isEditing === true ? 'Edit' : 'Add'} Competition</h5>
                <button id="addCompetitionAndQualifiedTeamsModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={isEditing === true ? updateCompetition : addCompetition}>
                <div className="modal-body">

                  <div className="">
                    <h6>Choose Program</h6>
                    <div className="row mb-3 mx-1">
                      {
                        slotPackages.map((slot, index) => (

                          <div key={index} className={`col-lg-2 col-6 border border-muted p-0`}>

                            <input disabled={isSubmitting} type="radio" name="slotpackage" defaultChecked={selectedSlot === slot.id} className="btn-check" id={'slotpackage' + index} autoComplete="off" />
                            <label className={`btn rounded-0 ${selectedSlot === slot.id && 'btn-primary'} btn-muted w-100`} onClick={() => setSelectedSlot(slot.id)} htmlFor={'slotpackage' + index}>

                              <div className='fs-6 text-center'>
                                {slot.program}
                              </div>

                            </label>

                          </div>

                        ))
                      }
                    </div>
                  </div>

                  <div className="mb-3">
                    <label htmlFor="modalsport" className='h6'>Select Sport</label>
                    <select className="form-select form-control-lg" id="modalsport" placeholder="Sport">
                      {
                        availableSports.map((sport, key) => (
                          <option key={key} value={sport}>{sport}</option>
                        ))
                      }
                    </select>
                  </div>

                  <div className="mb-3">
                    <label htmlFor="modalregion" className='h6'>Select Region</label>
                    <select className="form-select form-control-lg" id="modalregion" placeholder="Region">
                      <option value="World Wide">World Wide</option>
                      <optgroup label="Continental">
                        {
                          ["Asia", "Europe", "Africa", "Oceania", "North America", "Antarctica", "South America"].map((region, key) => (
                            <option key={key} value={region}>{region}</option>
                          ))
                        }
                      </optgroup>
                      <optgroup label="National">
                        {
                          countries.map((region, key) => (
                            <option key={key} value={region.country}>{region.country}</option>
                          ))
                        }
                      </optgroup>

                    </select>
                  </div>

                  <div className="mb-3">
                    <h6>Competition</h6>
                    <input id="modalcompetition" type="text" className="form-control" placeholder="Competition" />
                  </div>

                  <div className="mg-3">
                    <h6>Qualified Teams</h6>

                    <div id="qualifiedTeamList">
                      <div className="input-group mb-2">
                        <input name="qualifiedTeam[]" type="text" className="form-control" placeholder="Qualified Team" />
                        <span style={{ cursor: 'pointer' }} onClick={(e) => { e.target.parentElement.outerHTML = ''; }} className="input-group-text px-lg-3 alert-danger">-</span>
                      </div>
                    </div>
                  </div>

                  <div className="clearfix">
                    <button type="button" className="float-end btn btn-sm btn-outline-muted" onClick={
                      () => {
                        query("#qualifiedTeamList")[0].insertAdjacentHTML('beforeEnd', `
                        <div class="input-group mb-2">
                          <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" />
                          <span style="cursor: pointer;" onClick="parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
                        </div>
                        `);
                      }
                    }>
                      <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
                    </button>
                  </div>

                </div>
                <div className="modal-footer">
                  <button type="reset" className="btn btn-muted text-danger px-5" onClick={
                    () => {
                      query("#qualifiedTeamList")[0].innerHTML = `
                        <div class="input-group mb-2">
                          <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" />
                          <span style="cursor: pointer;" onClick="parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
                        </div>
                      `;
                    }
                  }>Reset</button>
                  <button type="submit" className="btn btn-primary px-5">{isEditing === true ? 'Update' : 'Save'}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const addCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let sport = query("#modalsport")[0];
    let region = query("#modalregion")[0];
    let competition = query("#modalcompetition")[0];
    let qualifiedTeams = [];
    query("input[name='qualifiedTeam[]']").forEach(function (elem) {
      qualifiedTeams.push(elem.value);
    });

    let body = new FormData();
    body.append('slotpackage', selectedSlot)
    body.append('sport', sport.value);
    body.append('region', region.value);
    body.append('competition', competition.value);
    body.append('qualified_teams', JSON.stringify(qualifiedTeams));

    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/competitions/create', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Competition added successfully`, 'success');
        setCompetitions(resp.data)
        setFilteredCompetitions(resp.data)
        setCurrentCompetitions(resp.data[0]);
        query("#addCompetitionAndQualifiedTeamsModalDismissBtn")[0].click();
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  const editCompetition = () => {
    setSelectedSlot(currentCompetition.slotpackage)
    query('#modalsport')[0].value = currentCompetition.sport;
    query('#modalcompetition')[0].value = currentCompetition.competition;
    query('#modalregion')[0].value = currentCompetition.region;
    query("#qualifiedTeamList")[0].innerHTML = '';
    query("#addCompetitionAndQualifiedTeamsModalTrigger")[0].click();
    JSON.parse(currentCompetition.qualified_teams).forEach((team) => {
      query("#qualifiedTeamList")[0].innerHTML += `
      <div class="input-group mb-2">
      <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" value="${team}" />
      <span style="cursor: pointer;" onClick="parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
        </div>
        `;
    });
    setIsEditing(true)
  }

  const updateCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let sport = query("#modalsport")[0];
    let region = query("#modalregion")[0];
    let competition = query("#modalcompetition")[0];
    let qualifiedTeams = [];
    query("input[name='qualifiedTeam[]']").forEach(function (elem) {
      qualifiedTeams.push(elem.value);
    });

    let body = new FormData();
    body.append('competitionId', currentCompetitionId);
    body.append('slotpackage', selectedSlot)
    body.append('sport', sport.value);
    body.append('region', region.value);
    body.append('competition', competition.value);
    body.append('qualified_teams', JSON.stringify(qualifiedTeams));

    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/competitions/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Competition updated successfully`, 'success');
        setCompetitions(resp.data)
        setFilteredCompetitions(resp.data)
        setIsEditing(false)
        setCurrentCompetitions(resp.data[0]);
        query("#addCompetitionAndQualifiedTeamsModalDismissBtn")[0].click();
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  const deleteCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('competitionId', currentCompetitionId);
      let resp = await request('/api/admin/competitions/delete', "POST", body, headers)

      if (resp.status === true) {
        setCompetitions(resp.data);
        setCurrentCompetitions(resp.data[0]);
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
  }

  return (
    <>
      <DashboardLayout page='/competitions'>
        <section className='container my-3 px-3'>

          <div className='mb-3'>
            <div className='d-flex justify-content-between align-items-center pb-2 pb-lg-0'>
              <h1 className='fw-normal fs-2'>Competitions</h1>
              <button className="btn btn-sm btn-outline-muted" id="addCompetitionAndQualifiedTeamsModalTrigger" data-bs-toggle="modal" data-bs-target="#addCompetitionAndQualifiedTeamsModal" onClick={() => {
                setIsEditing(false)
                query("#qualifiedTeamList")[0].innerHTML = '';
              }}>
                <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
                <span className="ms-2 d-md-inline-block d-none">ADD COMPETITION</span>
              </button>
            </div>
            <hr className='my-0' />
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                <div className="mb-lg-4 mb-2">
                  <label htmlFor="sport" className='h6'>
                    Filter by sport
                  </label>
                  <select onChange={filter} className="form-select form-control-lg" id="sport" placeholder="Sport">
                    {
                      availableSports.map((sport, key) => (
                        <option key={key} value={sport}>{sport}</option>
                      ))
                    }
                  </select>
                </div>

                <div className="">

                  <div className="row">
                    <div className="col-lg-3 col-md-5 mb-3">
                      <div className="">

                        <h5>Competitions</h5>

                        <div className='mt-0 mt-lg-4 w-100 mb-3'>
                          <div className='mb-1'>
                            <FontAwesomeIcon icon={faGlobeAfrica}></FontAwesomeIcon>
                            <span className='ms-1'>Filter by region</span>
                          </div>
                          <select onChange={filter} className="form-select form-control-lg" id="region" placeholder="Region">
                            <option value="">All</option>
                            <option value="World Wide">World Wide</option>
                            <optgroup label="Continental">
                              {
                                ["Asia", "Europe", "Africa", "Oceania", "North America", "Antarctica", "South America"].map((region, key) => (
                                  <option key={key} value={region}>{region}</option>
                                ))
                              }
                            </optgroup>
                            <optgroup label="National">
                              {
                                countries.map((region, key) => (
                                  <option key={key} value={region.country}>{region.country}</option>
                                ))
                              }
                            </optgroup>

                          </select>
                        </div>

                        <div className="mt-">
                          {renderCompetitions()}
                        </div>

                      </div>
                    </div>

                    <div className="col-lg-9 col-md-7">
                      <div className="">

                        <div className='mb-3 d-flex justify-content-between align-items-center'>
                          <h5>Competition and Qualified Teams</h5>

                          <div className='d-flex'>
                            <button className="btn btn-sm btn-muted text-danger me-1" onClick={deleteCompetition}>
                              <FontAwesomeIcon icon={faTrashAlt}></FontAwesomeIcon>
                            </button>
                            <button className="btn btn-sm btn-outline-muted" onClick={editCompetition}>
                              <FontAwesomeIcon icon={faPencilAlt}></FontAwesomeIcon>
                              <span className="ms-2 d-md-inline-block d-none">UPDATE</span>
                            </button>
                          </div>

                        </div>

                        <div className="">
                          {renderCompetitionQualifiedTeams()}
                        </div>

                      </div>
                    </div>

                  </div>

                </div>

                {addCompetitionAndQualifiedTeamsModal()}

                <div className="clearfix mb-5">
                  <a className="float-end btn btn-muted d-md-none d-inline-block" href="#top">
                    <FontAwesomeIcon icon={faAngleUp}></FontAwesomeIcon>
                  </a>
                </div>

              </div>
          }
        </section>
      </DashboardLayout>
    </>
  );
}
