import React, { useEffect, useState } from 'react';
import { DashboardTopBar, Footer } from '../../../components';
import { query, request, alert, appUrl } from '../../../utils';
import { useHistory } from 'react-router-dom';
import { useAuthStore } from '../../../store/auth';
import { useUserStore } from '../../../store/user';
import { useCallback } from 'react';
import { Helmet } from 'react-helmet';


export const Verify = () => {

  const [isSubmitting, setIsSubmitting] = useState(false)
  const history = useHistory()
  const { auth } = useAuthStore()
  const { user } = useUserStore()

  const verifyToken = async (e) => {
    e.preventDefault();

    setIsSubmitting(true)
    let token = query('#verificationToken')[0].value;

    let body = new FormData();
    body.append('token', token);

    let headers = new Headers();
    headers.append('AuthToken', auth.token);

    try {
      let resp = await request('/api/verifytoken', 'POST', body, headers);

      if (resp.status === true) {
        alert(`${auth.email} is verified successfully`, 'success');
        user.verification_status = resp.data.verification_status;

        history.push('/dashboard');
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
        setIsSubmitting(false)
      }
    } catch (error) {
      alert('Network Error', 'error')
      setIsSubmitting(false)
    }

  }

  const sendToken = useCallback(async () => {
    let headers = new Headers();
    headers.append('AuthToken', auth.token);

    try {
      let resp = await request('/api/sendtoken', 'POST', null, headers);

      if (resp.status === false) {
        alert(`Verfication token was not sent to ${auth.email}`, 'error');
      }
    } catch {
      alert('Network Error', 'error')
    }
  }, [auth.email, auth.token])

  useEffect(() => {
    if (user.verification_status === "verified") {
      history.push('/dashboard');
    } else {
      sendToken();
    }
  }, [auth.role, history, sendToken, user.verification_status])

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | Verify Account</title>
        <link rel="canonical" href={`${appUrl}/verify-account`} />
        <meta name="description" content={"Sports Fans Club is a platform that creates the opportunity for individuals to become true fans of their respective sport teams."} />
      </Helmet>

      <div className="bg-light w-100">
        <DashboardTopBar></DashboardTopBar>

        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-6 col-md-10 col-12'>

            <div className="text-center mb-lg-4 mb-md-5 mb-5">
              <div className='bruno-ace text-primary fw-light fs-1 d-lg-none'>Account Verification</div>
              <h1 className='bruno-ace text-primary fw-light d-none d-lg-inline-block'>Account Verification</h1>
              <div>Enter the 5 digit token sent to your email address ({auth.email})</div>
            </div>

            <form id='loginForm' onSubmit={(e) => verifyToken(e)} noValidate={true} >

              <div className="form-floating mb-4">
                <input type="text" disabled={isSubmitting} className="form-control rounded-0" id="verificationToken" placeholder="******" pattern="[0-9]{6}" required />
                <label htmlFor="verificationToken">Verification Token</label>
              </div>

              <button className='btn btn-lg w-100 btn-primary px-5' disabled={isSubmitting} type="submit">
                {
                  isSubmitting === true
                    ? <div>
                      <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                      <span className='ms-2 align-middle'>Verifying your account...</span>
                    </div>
                    : <span className='ms-2 align-middle'>Verify</span>
                }
              </button>

              <div className="my-4 clearfix">
                <button
                  type='button' onClick={sendToken}
                  className='btn btn-sm btn-muted float-end'
                >Request another token</button>
              </div>

            </form>

          </div >

        </section >

        <Footer />
      </div>
    </>
  );
}