import { Footer, Header } from "../../../components";
import { useCallback, useState, useEffect } from "react";
import { alert, appUrl, parseUrlQuery, query, request } from "../../../utils";
import { Link, useHistory } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";
import { Helmet } from "react-helmet";

export const Register = () => {

  const history = useHistory();

  const [isSubmitting, setIsSubmitting] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [slots, setSlots] = useState([])
  const [selectedSlot, setSelectedSlot] = useState(0)
  const [referralCode] = useState('')
  const [useOrgReferralCode, setUseOrgReferralCode] = useState(false)
  const [ORG_REFERRAL_CODE] = useState('SPORTSFANS')
  const [recaptchaToken, setRecaptchaToken] = useState(null)

  const submitRegisteration = async (e) => {
    e.preventDefault();
    let registerationForm = query('#registerationForm')[0];

    if (!registerationForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      registerationForm.classList.add('was-validated')
    } else {
      setIsSubmitting(true)

      // // verify reCaptcha
      // let recaptchaBody = new FormData();
      // recaptchaBody.append('secret', '6Le6ceEaAAAAALQ-_sjy9Ck4AntAWbHmWt9btpoj');
      // recaptchaBody.append('response', recaptchaToken ?? '');
      // let verifyRecaptchaToken = await request('https://www.google.com/recaptcha/api/siteverify', "POST", recaptchaBody, {}, true);
      // if (verifyRecaptchaToken.success === false) {
      //   alert('reCaptcha verification failed', 'error', false);
      //   handleExpire()
      //   setIsSubmitting(false)
      //   return;
      // }

      setIsSubmitting(true)
      let email = query('#registerEmail')[0].value;
      let password = query('#registerPassword')[0].value;
      let cpassword = query('#registerConfirmPassword')[0].value;
      let referralcode = query('#registerReferralCode')[0].value;

      let body = new FormData();
      body.append('email', email);
      body.append('password', password);
      body.append('cpassword', cpassword);
      body.append('slot', selectedSlot);
      body.append('referralcode', referralcode === ORG_REFERRAL_CODE ? "" : referralcode);

      try {
        let resp = await request('/api/register', 'POST', body);

        if (resp.status === true) {
          alert('Your account have been created successfully, Your slot is being created.', 'success', true);

          setTimeout(async () => {
            // choose slot for member
            let body = new FormData();
            body.append('email', email);
            body.append('slot', selectedSlot);

            let slotresp = await request('/api/auto-choose-slot', 'POST', body);

            if (slotresp.status === true) {
              alert(slotresp['message'] + '<br />Your slots have been created successfully, Login to continue.', 'success', true);
              setTimeout(() => {
                history.push('/login');
              }, 5000);
            } else {
              alert('Your slot was not selected, Login to select the slot manually', 'error')
              setTimeout(() => {
                history.push('/login');
              }, 5000);
            }

            setIsSubmitting(false)
          }, 3000);

        } else {

          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error')
          setIsSubmitting(false)
        }
      } catch (error) {
        alert('Network Error', 'error', false)
        setIsSubmitting(false)
      }

    }

  }

  const handleToken = (token) => {
    setRecaptchaToken(token)
  }

  const handleExpire = () => {
    setRecaptchaToken(null);
  }

  const getSlots = useCallback(async () => {
    try {
      let resp = await request('/api/slots', 'GET');

      if (resp.status === true) {
        setSlots(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [])

  useEffect(() => {
    console.log('[useEffect]')
    let query = parseUrlQuery();
    if (query.slotId !== undefined) {
      setSelectedSlot(query.slotId)
    }

    getSlots()
  }, [getSlots])

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | Register</title>
        <link rel="canonical" href={`${appUrl}/register`} />
        <meta name="description" content='Register to create an account' />
      </Helmet>

      <div className="bg-light w-100">
        <Header />

        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-6 col-md-10 col-12'>

            <div className="text-center mb-lg-4">
              <div className='bruno-ace text-primary fw-light fs-1 d-lg-none'>Register</div>
              <h1 className='bruno-ace text-primary fw-light d-none d-lg-inline-block'>Register</h1>
              <div>Already have an account? <Link className='text-primary' to='/login'>Login</Link></div>
            </div>

            <form id='registerationForm' onSubmit={submitRegisteration} noValidate={true} >

              <div className="form-floating mb-4">
                <input type="email" disabled={isSubmitting} className="form-control rounded-0" id="registerEmail" placeholder="name@example.com" required />
                <label htmlFor="registerEmail">Email address</label>
                <div className="invalid-feedback">Please enter an email address.</div>
              </div>

              <div className="form-floating mb-1 clearfix">
                <input type={showPassword === true ? "text" : "password"} disabled={isSubmitting} className="form-control rounded-0" id="registerPassword" placeholder='******' required />
                <label htmlFor="registerPassword">Password</label>
                <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                  setShowPassword(!showPassword)
                }
                }>{showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                <div className="invalid-feedback">Please enter a secured password.</div>
              </div>

              <div className="form-floating mb-0 clearfix">
                <input type={showConfirmPassword === true ? "text" : "password"} disabled={isSubmitting} className="form-control rounded-0" id="registerConfirmPassword" placeholder='******' required />
                <label htmlFor="registerConfirmPassword">Confirm Password</label>
                <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                  setShowConfirmPassword(!showConfirmPassword)
                }
                }>{showConfirmPassword === true ? 'Hide Password' : 'Show Password'}</div>
                <div className="invalid-feedback">Please enter a secured password.</div>
              </div>
              <div className='text-ce nter mb-2 mt-0'>
                <span className='text-primary lh-5' style={{ fontSize: '14px' }}>Password should contain at least 8 characters, 1 uppercase, 1 lowercase and 1 number.</span>
              </div>

              <label className="">
                Select a Slot Program <br />
                <span className='fw-500'>
                  {
                    (slots.length > 0 && selectedSlot > 0)
                      ?
                      `${slots.find(slot => slot.id === selectedSlot).program} Program`
                      :
                      'No slot selected'
                  }
                </span>
              </label>

              <div className='d-flex justify-content-start overflow-auto mb-4'>

                {
                  slots.map((slot, index) => (

                    <div key={index} className={`col-lg-4 col-5 card shadow-sm ${index > 0 && 'ms-lg-3 ms-1'} me-lg-3 me-1 my-3`}>

                      <input disabled={isSubmitting} type="radio" name="registerSlot" defaultChecked={selectedSlot === slot.id} className="btn-check" id={'registerSlot' + index} autoComplete="off" />
                      <label className="btn w-100 h-100 btn-outline-primary border-0" onClick={() => setSelectedSlot(slot.id)} htmlFor={'registerSlot' + index}>

                        <div className='fs-6 fw-500 text-center'>
                          {slot.program} <br />
                          <span className="fs-6">Program</span>
                        </div>

                      </label>

                    </div>

                  ))
                }

              </div>

              <div className="form-floating mb-4">
                <input type="text" pattern='[a-zA-Z0-9]{7}' maxLength='7' readOnly={useOrgReferralCode} disabled={isSubmitting} className="form-control rounded-0" id="registerReferralCode" placeholder='1234567' defaultValue={useOrgReferralCode === true ? ORG_REFERRAL_CODE : referralCode} required />
                <label htmlFor="registerReferralCode">Referral Code</label>
                <div className="invalid-feedback">
                  <span>Please use a referral code or check the box below to use default code.</span>
                </div>
                <div>
                  <input type="checkbox" className="form-check-input" id="useOrfRefCode"
                    onChange={() => { setUseOrgReferralCode(!useOrgReferralCode) }} />
                  <label className='form-check-label ms-2 text-primary' htmlFor="useOrfRefCode">
                    <small>Use default Referral Code</small>
                  </label>
                </div>
              </div>

              <div className="form-check mb-4">
                <input className="form-check-input" type="checkbox" id="registerTermsAndCondition" required disabled={isSubmitting} />
                <label className="form-check-label" htmlFor="registerTermsAndCondition">I agree to the <Link className='text-primary' to='/terms-and-conditions'>Terms and Conditions</Link></label>
                <div className="invalid-feedback">Please accept our terms and conditions.</div>
              </div>

              {/* reCAPTCHA */}
              <ReCAPTCHA
                sitekey="6Le6ceEaAAAAAI5IIaO9-Jh_-0SXpzw8BMqBILOR"
                onChange={() => handleToken}
                className="mb-3"
              ></ReCAPTCHA>

              <button className='w-100 btn btn-lg btn-primary px-5' disabled={isSubmitting} type="submit">
                {
                  isSubmitting === true
                    ? <div>
                      <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                      <span className='ms-2 align-middle'>Creating your account...</span>
                    </div>
                    : <span className='ms-2 align-middle'>Register</span>
                }
              </button>

            </form>

          </div >

        </section >

        <Footer />
      </div>
    </>
  )
}