import { faArrowRight, faCheck } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { formatDate, formatMoney, request } from '../../../utils';
import { DashboardLayout } from '../../../components';
import { useCallback } from 'react';
import { useAuthStore } from '../../../store/auth';

export const Dashboard = () => {
  const { auth } = useAuthStore()

  const [state, setState] = useState({
    userPackage: {},
    activeSlot: {},
    recentUnreadNotifications: [],
    totalCashBenefitAccrued: 0,
    recentPendingBenefits: [],
    pageIsLoading: true,
    booted: false
  })

  const getDashboard = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let resp = await request('/api/dashboard', 'GET', null, headers);
      if (resp.status === true) {
        let dashboard = resp.data;
        setState({
          ...state,
          userPackage: dashboard.userPackage ?? {},
          activeSlot: dashboard.activeSlot ?? {},
          recentUnreadNotifications: dashboard.recentUnreadNotifications,
          totalCashBenefitAccrued: dashboard.totalCashBenefitAccrued,
          recentPendingBenefits: dashboard.recentPendingBenefits,
          pageIsLoading: false
        });
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token, state])

  useEffect(() => {
    if (state.pageIsLoading === false) return
    getDashboard();
    return () => { }
  }, [getDashboard, state.pageIsLoading])

  return (
    <>
      <DashboardLayout page='/dashboard'>

        <section className='container my-3 px-3 text-muted'>

          <div className='mb-3 text-dark'>
            <h1 className='fw-normal fs-2'>Dashboard</h1>
            <hr className='my-0' />
          </div>

          {
            state.pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                {/* Package and Slots */}
                <div className='container'>
                  <div className='row'>

                    <div className='col-lg-7 col-12'>
                      <div className='mb-3 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>

                        <div className='d-flex justify-content-between'>
                          <div className='fw-500 text-primary'>Your Program</div>
                        </div>

                        <div className='my-4'>
                          <h2 className='lh-0 mb-5 fs-3'>{state.userPackage.slot_program} Program</h2>
                        </div>

                        <div className='d-flex justify-content-between '>
                          <div className='text-primary fs-6'>Current Level: {state.userPackage.rank}</div>
                          <div className='fs-6'>{state.userPackage.status === 'pending' ? <Link to={'/payment-invoice'} className='btn btn-primary btn-sm m-0 py-0 px-2'>Activate</Link> : <span>{state.userPackage.status}</span>}</div>
                        </div>
                      </div>
                    </div>

                    <div className='col-lg-5 col-12'>
                      <div className='mb-3 rounded border border-light shadow-sm px-lg-4 px-3 pt-3 pb-0'>

                        <div className='d-flex justify-content-between align-items-top'>
                          <div className='fw-500 text-primary'>Number of Invites</div>
                          <div style={{ fontSize: '14px' }}>
                            {state.activeSlot.referrals_acquired}/{state.activeSlot.referrals_required}
                          </div>
                        </div>

                        <div className='my-5'>

                          <div className="progress">
                            <div className={`progress-bar progress-bar-striped ${state.activeSlot.status === 'pending' ? 'bg-muted' : 'bg-primary'} ${state.activeSlot.status === 'active' && 'progress-bar-animated'}`} role="progressbar" style={{ width: `${Math.ceil(((state.activeSlot.referrals_acquired) / state.activeSlot.referrals_required) * 100)}%` }} >{Math.floor((state.activeSlot.referrals_acquired / state.activeSlot.referrals_required) * 100)}%</div>
                          </div>

                          <div className='d-flex justify-content-between '>
                            <div className='fs-4'>{formatMoney(state.activeSlot.cash, 0)}</div>
                            <div style={{ fontSize: '14px' }}>{state.activeSlot.status}</div>
                          </div>

                        </div>

                      </div>
                    </div>

                  </div>
                </div>

                {/* Total Cash Benefits */}
                <div className='container mb-3'>
                  <div className='rounded border border-light shadow-sm px-lg-4 px-3 pt-3 pb-0'>

                    <div className='my-3 row'>
                      <div className='col-lg-6 col-md-6 col-5 text-primary d-flex align-items-center fw-500'>Total<br />Cash Benefits</div>
                      <div className='col-lg-6 col-md-6 col-7'>
                        <span className='display-3'>
                          {formatMoney(state.totalCashBenefitAccrued, 0)}
                        </span>
                      </div>
                    </div>

                  </div>
                </div>

                {/* Benefits */}
                <div className='container mb-3'>
                  <h4 className='ms-lg-2 ms-2'>Recent Accrued Benefits</h4>
                  {
                    state.recentPendingBenefits.map((benefit, index) => (

                      <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                        <div className='d-flex justify-content-between align-items-center'>
                          <div className='fw-500 lh-0 text-primary'>{benefit.achievement}</div>
                          <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                        </div>

                        <div className='fs-6 lh-0 mt-3'>
                          {
                            benefit.benefit === null ? "No souvenir" : benefit.benefit
                          }
                        </div>

                        <div className='d-flex justify-content-between '>
                          <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                          <div>
                            {
                              benefit.status === 'pending' &&
                              <div className='' style={{ fontSize: '14px' }}>pending...</div>
                            }
                            {
                              benefit.status === 'given' &&
                              <>
                                <div className='text-success' style={{ fontSize: '14px' }}>
                                  given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon>
                                </div>
                              </>
                            }
                            {
                              benefit.status === 'cancelled' &&
                              <div className='text-danger' style={{ fontSize: '14px' }}>cancelled</div>
                            }
                          </div>

                        </div>
                      </div>

                    ))
                  }
                  <div className='d-flex justify-content-end'>
                    <Link to={'/benefits'} className='btn btn-primary btn-sm mt-3'>See All<FontAwesomeIcon className='ms-3 ' icon={faArrowRight}></FontAwesomeIcon></Link>
                  </div>

                </div>

                {/* Notifications */}
                <div className='container mb-3'>
                  {
                    state.recentUnreadNotifications.length > 0 &&
                    <>
                      <h4 className='ms-lg-2 ms-2'>{state.recentUnreadNotifications.length} Unread Notifications</h4>
                      {
                        state.recentUnreadNotifications.map((notice, index) => (

                          <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                            <div className='d-flex justify-content-between align-items-top'>
                              <div className='lh-0'>{notice.message === null ? "No message" : notice.message}</div>
                            </div>

                            <div className='mt-2' style={{ fontSize: '14px' }}>{formatDate(notice.created_at)}</div>

                          </div>
                        ))
                      }
                      <div className='d-flex justify-content-end'>
                        <Link to={'notifications'} className='btn btn-primary btn-sm mt-3'>See All<FontAwesomeIcon className='ms-3 ' icon={faArrowRight}></FontAwesomeIcon></Link>
                      </div>
                    </>
                  }

                </div>

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  )
}
