import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { alert, formatDate, formatMoney, formatNumber, query, request } from '../../../utils';
import { faEdit, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { useAuthStore } from '../../../store/auth';
import { DashboardLayout } from '../../../components';
import { useCallback } from 'react';

export const SlotPackages = () => {

  const [slotPackages, setSlotPackages] = useState([])
  const [editingLevel, setEditingLevel] = useState({})
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getSlotPackages = useCallback(async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/slots', 'GET', null, headers);

      if (resp.status === true) {
        setSlotPackages(resp.data)
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  useEffect(() => {
    getSlotPackages();
    setPageIsLoading(false)
  }, [getSlotPackages])

  const addSlotPackageModal = () => {
    return (
      <div>
        <div className="modal fade" id="addSlotPackageModal" tabIndex={-1} aria-labelledby="addSlotPackageModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-dialog- centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addSlotPackageModalLabel">Editing {editingLevel.program} Program</h5>
                <button id="addSlotPackageModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={updateSlotPackage}>
                <div className="modal-body">
                  <div className="mb-3">
                    <label htmlFor="slotprogram" className="form-label">Rank</label>
                    <input type="text" readOnly={true} autoComplete={""} className="form-control" id="slotprogram" placeholder="Slot Package" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="no_slots" className="form-label">Number of slot</label>
                    <input type="number" readOnly className="form-control form-control" id="no_slots" placeholder="no. of slots" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="cost" className="form-label">Cash Benefit</label>
                    <input type="" autoComplete={""} className="form-control" id="cost" placeholder="Cash benefit" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="slotbenefits" className="form-label">Slot Benefit</label>
                    <textarea id="slotbenefits" cols="30" rows="5" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                  </div>
                </div>
                <div className="modal-footer">
                  <button type="submit" className="btn btn-primary px-5">Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const editSlotPackage = (slotpackage) => {
    setEditingLevel(slotpackage)
    query('#slotprogram')[0].value = slotpackage.program;
    query('#no_slots')[0].value = slotpackage.no_slots;
    query('#cost')[0].value = slotpackage.cost;
    query('#slotbenefits')[0].value = slotpackage.benefits;
    query('#addSlotPackageModalToggle')[0].click();
  }

  const noSlotPackages = () => {
    return <tr>
      <td colSpan={7} className='text-center'>No Programs.</td>
    </tr>
  }

  const renderSlotPackages = () => {
    return slotPackages.map((slotpackage, index) => (
      <tr>
        <td>{index + 1}</td>
        <td>{slotpackage.program}</td>
        <td>{formatNumber(slotpackage.no_slots)} slot</td>
        <td>{formatMoney(slotpackage.cost, 0)}</td>
        <td>{slotpackage.benefits}</td>
        <td>{formatDate(slotpackage.updated_at) ?? ""}</td>
        <td>
          <FontAwesomeIcon className="mx-2" title={`edit ${slotpackage.rank}`} style={{ cursor: "pointer" }} icon={faEdit} onClick={() => editSlotPackage(slotpackage)}></FontAwesomeIcon>
        </td>
      </tr>
    ))
  }

  const updateSlotPackage = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let rank = query('#slotprogram')[0];
    let referrals_required = query('#no_slots')[0];
    let cost = query('#cost')[0];
    let benefits = query('#slotbenefits')[0];

    let body = new FormData();
    body.append('id', editingLevel.id);
    body.append('program', rank.value);
    body.append('no_slots', referrals_required.value);
    body.append('cost', cost.value);
    body.append('benefits', benefits.value);

    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/slots/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Slot package updated successfully`, 'success');
        setSlotPackages(resp.data)
        query("#addSlotPackageModalDismissBtn")[0].click();
        referrals_required.value = ""; cost.value = ""; benefits.value = "";
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  return (
    <>
      <DashboardLayout page='/packages'>
        <section className='container my-3 px-3'>

          <div className='mb-3'>
            <h1 className='fw-normal fs-2'>Programs</h1>
            <hr className='my-0' />
          </div>
          <button id='addSlotPackageModalToggle' className="d-none btn btn-sm btn-outline-secondary" data-bs-toggle="modal" data-bs-target="#addSlotPackageModal"></button>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                <div>
                  {
                    slotPackages.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      There are no Slot Packages
                    </div>
                  }
                </div>

                {slotPackages.length > 0 &&
                  <>
                    <div className="table-responsive">
                      <table className="table table-striped table-sm">
                        <thead>
                          <tr>
                            <th className='fw-500'>#</th>
                            <th className='fw-500'>Slot&nbsp;Package</th>
                            <th className='fw-500'>No.&nbsp;of&nbsp;Slots</th>
                            <th className='fw-500'>Cost</th>
                            <th className='fw-500'>Benefits</th>
                            <th className='fw-500'>Date&nbsp;Updated</th>
                            <th className='fw-500'></th>
                          </tr>
                        </thead>
                        <tbody>
                          {slotPackages.length > 0 ? renderSlotPackages() : noSlotPackages()}
                        </tbody>
                      </table>
                    </div>
                  </>
                }

                {addSlotPackageModal()}

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
