import { useCallback } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { formatMoney, request } from '../../../../utils';
import './Benefits.scss';

export const Benefits = () => {

  const [benefits, setBenefits] = useState([])

  const transformProgramBenefitMap = useCallback((benefits) => {
    console.log('[transformBenefits]')
    let newBenefits = [[], [], [], [], [], []]
    const tags = ["LOCAL", "FOREIGN", "INTERNATIONAL", "CONTINENTAL", "WORLD CUP", "OLYMPIC"]

    benefits.forEach((level, i) => {
      level.benefits.forEach((benefit, j) => {
        newBenefits[j][i] = benefit
      })
    })

    newBenefits.map((benefit, i) => {
      benefit.splice(0, 0, tags[i])
      return [...benefit]
    })

    setBenefits(newBenefits)
  }, [])

  const getBenefits = useCallback(async () => {
    try {
      let resp = await request('/api/referrallevels');
      let referrallevels = resp.status === true ? resp.data : [];
      console.log(resp.data)
      console.log('[setBenefits]')
      setBenefits(referrallevels)
      transformProgramBenefitMap(referrallevels)
    } catch (error) {
      console.log(error);
    }
  }, [transformProgramBenefitMap])

  useEffect(() => {
    console.log('[useEffect]')
    getBenefits()
  }, [getBenefits])

  return (
    <div>

      <div className="__getting_your_benefits my-lg-5 my-md-5 my-3">
        <a className='__namelink' href='/' name="getting-your-benefits">{ }</a>

        <div className='container h-100 d-flex align-items-center'>
          <div className='w-100'>
            <h2 className='bruno-ace text-primary fs-4 mb-lg-4'>
              GETTING YOUR BENEFITS
            </h2>
            <p className='w-90'>
              The benefits accruable to members is dependent on the sport program they registered with and the number of persons they are able to bring into the platform as they progress through every level. The table below explains more.
            </p>
            <div className='w-100 overflow-auto mt-lg-4 mb-lg-1 mt-md-4 mb-md-2 mt-3 mb-3'>
              <table className='table table-bordered'>
                <thead>
                  <tr>
                    <th></th>
                    <th colSpan={7} className='fw-500 bg-grey py-lg-4 text-center'>LEVELS</th>
                  </tr>
                  <tr>
                    <th className='fw-500'>PROGRAMS&darr;</th>
                    <th className='fw-500'>MANAGER</th>
                    <th className='fw-500'>COACH</th>
                    <th className='fw-500'>PLAYERS</th>
                    <th className='fw-500'>SUPPORTERS</th>
                    <th className='fw-500'>STADIUM</th>
                    <th className='fw-500'>FANS</th>
                    <th className='fw-500'>TROPHY</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    benefits.length > 0 ?
                      benefits.map(benefit => {
                        return (
                          <tr>
                            <th className='fw-500'>{benefit[0]}</th>
                            <td>
                              {benefit[1]?.souvenir} <br />
                              {benefit[1]?.cash > 0 ? formatMoney(benefit[1]?.cash, 0) : ''}
                            </td>
                            <td>
                              {benefit[2]?.souvenir} <br />
                              {benefit[2]?.cash > 0 ? formatMoney(benefit[2]?.cash, 0) : ''}
                            </td>
                            <td>
                              {benefit[3]?.souvenir} <br />
                              {benefit[3]?.cash > 0 ? formatMoney(benefit[3]?.cash, 0) : ''}
                            </td>
                            <td>
                              {benefit[4]?.souvenir} <br />
                              {benefit[4]?.cash > 0 ? formatMoney(benefit[4]?.cash, 0) : ''}
                            </td>
                            <td>
                              {benefit[5]?.souvenir} <br />
                              {benefit[5]?.cash > 0 ? formatMoney(benefit[5]?.cash, 0) : ''}
                            </td>
                            <td>
                              {benefit[6]?.souvenir} <br />
                              {benefit[6]?.cash > 0 ? formatMoney(benefit[6]?.cash, 0) : ''}
                            </td>
                            <td>
                              {formatMoney(benefit[7]?.cash, 0)}
                            </td>
                          </tr>
                        )
                      })
                      :
                      <tr colSpan={8}>Loading benefits...</tr>
                  }
                </tbody>
              </table>
            </div>
            <p className='w-100 w-md-90 w-lg-90 text-center'>
              A 10% CHARITY AND SUSTAINABILITY FEE IS DEDUCTABLE STARTING FROM LEVEL 4 (SUPPORTERS)
            </p>
          </div>
        </div>

      </div>

    </div >
  )
}