import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBasketballBall, faBell, faBoxes, faCrown, faMoneyCheckAlt, faPowerOff, faServer, faThLarge, faUser, faUsers, faUserShield } from '@fortawesome/free-solid-svg-icons';
import { faPaypal } from '@fortawesome/free-brands-svg-icons';
import { appUrl, baseUrl, confirm } from '../../utils';
import { useAuthStore } from '../../store/auth';
import { useUserStore } from '../../store/user';
import SportFansLogo from '../../assets/image/sportsfans_logo.png';
import defaultProfilePicture from '../../assets/image/default_profile_picture.png';
import './DashboardLayout.scss'
import { Helmet } from 'react-helmet';

export const DashboardTopBar = () => {

  const user = useUserStore(state => state.user);
  const { logout } = useAuthStore()

  const logOutUser = () => {
    confirm('Are you sure you want to logout?', () => {
      logout();
      window.location.href = '/login'
    })
  }

  return (
    < div className='col-lg-12 sticky-top w-100' >
      <nav className="navbar shadow-sm navbar-expand-lg navbar-light px-lg-3 bg-white">
        <div className="container px-2">
          <Link className="navbar-brand" to="/">
            <img src={SportFansLogo} style={{ width: '50px', height: '50px' }} alt="Sports Fans Club" />
            <span className='fw-bold ms-2 d-none d-lg-inline-block text-primary align-middle bruno-ace'>SPORTS FANS CLUB</span>
          </Link>
          <div className="" id="navbarTogglerDemo02">

            <div className="ms-auto d-flex align-middle">
              <Link to={'/notifications'} className="btn btn-muted fs-5 text-primary">
                <FontAwesomeIcon className={user.notifications > 0 ? 'text-primary' : 'text-muted'} icon={faBell} ></FontAwesomeIcon>
              </Link>
              <button onClick={logOutUser} className="btn btn-muted btn-sm ms-1 fs-5">
                <FontAwesomeIcon icon={faPowerOff} className='text-danger'></FontAwesomeIcon>
                <span>&nbsp;Logout</span>
              </button>

              <button className="d-md-none navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#sidebar" aria-controls="sidebar" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon" />
              </button>

            </div>
          </div>
        </div>
      </nav>
    </div >
  )
}

