import { faArrowLeft, faCheck, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { alert, formatDate, formatMoney, parseUrlQuery, request } from '../../../utils';
import { useAuthStore } from '../../../store/auth';
import { DashboardLayout } from '../../../components';
import { useHistory } from 'react-router-dom';
import { useCallback } from 'react';


export const UserBenefits = () => {

  const [isMember, setIsMember] = useState(false)
  const [member, setMember] = useState('Members')
  const [myBenefits, setMyBenefits] = useState([])
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()
  const history = useHistory()

  const getAllBenefits = useCallback(async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/admin/all-benefits', 'GET', null, headers);

      if (resp.status === true) {
        setMyBenefits(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  const getMemberBenefits = useCallback(async (email) => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('email', email);
      let resp = await request('/api/admin/member-benefits', 'POST', body, headers);

      if (resp.status === true) {
        setMyBenefits(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  const giveBenefit = async (id, userId) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('userId', userId)
      body.append('benefitId', id)
      let resp = await request('/api/admin/givebenefit', 'POST', body, headers);

      if (resp.status === true) {
        alert('Given successfully', 'success');
        isMember === false ? getAllBenefits() : getMemberBenefits(member);
      } else {
        alert('Not given', 'error');
      }
    } catch (error) {
      alert('Network Error')
      console.log(error);
    }
  }

  const cancelBenefit = async (id, userId) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('userId', userId)
      body.append('benefitId', id)
      let resp = await request('/api/admin/cancelbenefit', 'POST', body, headers);

      if (resp.status === true) {
        alert('Cancelled successfully', 'success');
        isMember === false ? getAllBenefits() : getMemberBenefits(member);
      } else {
        alert('Not cancelled', 'error');
      }
    } catch (error) {
      alert('Network Error')
      console.log(error);
    }
  }

  useEffect(() => {
    let query = parseUrlQuery();
    if (query.email !== undefined) {
      setIsMember(true)
      setMember(query.email)
      getMemberBenefits(query.email)
    } else getAllBenefits();
    setPageIsLoading(false)
  }, [getAllBenefits, getMemberBenefits])

  return (
    <>
      <DashboardLayout page='/membersbenefits'>
        <section className='container my-3 px-3 text-muted'>

          <div className='mb-2 text-dark'>
            <h1 className='fw-normal fs-2'>Members Benefits</h1>
            {myBenefits.length > 0 && <span>{member} have accrued {myBenefits.length} benefits</span>}
            <hr className='my-0' />
            {
              isMember === true &&
              <button className='btn btn-sm btn-muted' onClick={() => { history.goBack() }}>
                <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
              </button>
            }
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                <div>
                  {
                    myBenefits.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      No Accrued Benefits
                    </div>
                  }
                </div>

                <div className='text-muted mt-4'>
                  {
                    myBenefits.map((benefit, index) => (

                      <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                        <div className='d-flex justify-content-between align-items-top'>
                          <div className='fw-500 lh-0 text-primary'>
                            {benefit.achievement} <br />
                            <small>{benefit.email}</small>
                          </div>
                          <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                        </div>

                        <div className='fs-6 lh-0 mt-3'>
                          {
                            benefit.benefit === null ? "No souvenir" : benefit.benefit
                          }
                        </div>

                        <div className='d-flex justify-content-between align-items-center'>
                          <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                          <div>
                            {
                              benefit.status === 'pending' &&
                              <>
                                <button className='btn btn-sm pt-0 pb-1 btn-danger text-light' title="Mark this benefit as cancelled" onClick={() => cancelBenefit(benefit.id, benefit.user_id)}>cancel</button>
                                <button className='ms-1 btn btn-sm pt-0 pb-1 btn-primary' title="Mark this benefit as given" onClick={() => giveBenefit(benefit.id, benefit.user_id)}>given</button>
                              </>
                            }
                            {
                              benefit.status === 'given' &&
                              <>
                                <div className='text-success' style={{ fontSize: '14px' }}>
                                  given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon> <br />
                                  {formatDate(benefit.updated_at)}
                                </div>
                              </>
                            }
                            {
                              benefit.status === 'cancelled' &&
                              <>
                                <div className='text-danger' style={{ fontSize: '14px' }}>
                                  cancelled <br />
                                  {formatDate(benefit.updated_at)}
                                </div>
                              </>
                            }
                          </div>

                        </div>
                      </div>

                    ))
                  }
                </div>

              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
