import { faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { formatDate, request } from '../../../utils';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { useUserStore } from '../../../store/user';
import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';


export const Notifications = () => {

  const [unreadNotifications, setUnreadNotifications] = useState([])
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()
  const { user } = useUserStore()
  const history = useHistory()

  const getUnreadNotifications = useCallback(async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/unread-notifications', 'GET', null, headers);

      if (resp.status === true) {
        setUnreadNotifications(resp.data);
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  const openNotification = async (notice) => {
    // mark notification as read
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let body = new FormData();
      body.append('notificationId', notice.id)
      let resp = await request('/api/mark-as-read', 'POST', body, headers);

      if (resp.status === true && notice.route != null && notice.route !== '') {
        user.notifications -= 1;
        // if user is admin, route
        if (auth.role === 'admin') {
          notice.route = notice.route.replace('member', 'admin');
        }
        history.push(notice.route);
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  useEffect(() => {
    getUnreadNotifications();
    setPageIsLoading(false)
  }, [getUnreadNotifications, setPageIsLoading])

  return (
    <>
      {/* <div className='btn btn-sm btn-primary m-0 py-0 px-2' style={{ fontSize: '14px' }}>Mark as read</div> */}
      <DashboardLayout>
        <section className='container my-3 px-3'>

          <div className='mb-2'>
            <h1 className='fw-normal fs-2'>Notifications</h1>
            {unreadNotifications.length > 0 && <span>You have {unreadNotifications.length} unread notifications</span>}
            <hr className='my-0' />
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>

                <div>
                  {
                    unreadNotifications.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      No Unread Notifications
                    </div>
                  }
                </div>

                <div className='text-muted mt-4'>
                  {

                    unreadNotifications.map((notice, index) => (

                      <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3' style={{ cursor: 'pointer' }} onClick={() => openNotification(notice)}>
                        <div className='d-flex justify-content-between align-items-top'>
                          <div className='lh-0'>{notice.message === null ? "No message" : notice.message}</div>
                        </div>

                        <div className='mt-2' style={{ fontSize: '14px' }}>{formatDate(notice.created_at)}</div>

                      </div>

                    ))

                  }
                </div>
              </div>
          }
        </section>
      </DashboardLayout>
    </>
  );
}
