import { faCheck, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { useCallback } from 'react';
import { DashboardLayout } from '../../../components';
import { useAuthStore } from '../../../store/auth';
import { alert, formatDate, formatMoney, request } from '../../../utils';

export const MyBenefits = () => {
  const [myBenefits, setMyBenefits] = useState([])
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getMyBenefits = useCallback(async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/my-benefits', 'GET', null, headers);

      if (resp.status === true) {
        setMyBenefits(resp.data);
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }, [auth.token])

  const queryBenefits = async () => {
    // get my benefits
    try {
      // alert('HI');
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/querybenefits', 'POST', null, headers);

      if (resp.status === true) {
        alert('Queried successfully', 'success');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  useEffect(() => {
    getMyBenefits();
    setPageIsLoading(false)
  }, [getMyBenefits])

  return (
    <>
      <DashboardLayout page='/benefits'>
        <section className='container my-3 px-3 text-muted'>

          <div className='mb-2 text-dark'>
          <h1 className='fw-normal fs-2'>Your Benefits</h1>
            {myBenefits.length > 0 && <span>You have {myBenefits.length} accrued {myBenefits.length > 1 ? 'benefits' : 'benefit'}</span>}
            <hr className='my-0' />
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div>
                  {
                    myBenefits.length === 0 &&
                    <div className='my-5 py-5 d-flex flex-column align-items-center'>
                      <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      No Accrued Benefits
                    </div>
                  }
                </div>

                <div className='text-muted mt-4'>
                  {
                    myBenefits.map((benefit, index) => (

                      <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                        <div className='d-flex justify-content-between align-items-center'>
                          <div className='fw-500 lh-0 text-primary'>{benefit.achievement}</div>
                          <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                        </div>

                        <div className='fs-6 lh-0 mt-3'>
                          {
                            benefit.benefit === null ? "No souvenir" : benefit.benefit
                          }
                        </div>

                        <div className='d-flex justify-content-between align-items-center'>
                          <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                          <div>
                            {
                              benefit.status === 'pending' &&
                              <button className='btn btn-sm pt-0 pb-1 btn-primary' onClick={queryBenefits}>query</button>
                            }
                            {
                              benefit.status === 'given' &&
                              <>
                                <div className='text-success' style={{ fontSize: '14px' }}>
                                  given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon> <br />
                                  {formatDate(benefit.updated_at)}
                                </div>
                              </>
                            }
                            {
                              benefit.status === 'cancelled' &&
                              <>
                                <div className='text-danger' style={{ fontSize: '14px' }}>
                                  cancelled <br />
                                  {formatDate(benefit.updated_at)}
                                </div>
                              </>
                            }
                          </div>

                        </div>
                      </div>

                    ))
                  }
                </div>
              </div>
          }
        </section>
      </DashboardLayout>
    </>
  );
}
