import { useCallback, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useAuthStore } from "../../store/auth"
import { useUserStore } from "../../store/user";

export const AuthMiddleware = ({ role, permissions, children }) => {
  const { auth, logout } = useAuthStore();
  const { user } = useUserStore();
  const history = useHistory();

  const check = useCallback(() => {

    // check for authentication
    if (auth.authenticated === false) {
      console.log('user not authenticated')
      logout();
      history.replace('/login')
    }

    // check the role
    if (role !== undefined) {
      if (!role.includes(auth.role)) {
        console.log('user has unauthorized role')
        logout();
        history.replace('/login')
      }
    }

    // check permissions
    if (permissions !== undefined) {
      if (!auth.permissions.includes(permissions)) {
        console.log('user has no permission')
        logout();
        history.replace('/login')
      }
    }

    if (user.isset) {

      // redirect to verify email
      if (user.verification_status !== "verified") {
        console.log('user has not verified account')
        history.replace('/verify-account')
        return
      }

      // redirect to payment 
      if (user.payment_status === "pending") {
        console.log('user has not made payments')
        history.replace('/payment-invoice')
        return
      }

    }

  }, [auth.authenticated, auth.permissions, auth.role, history, logout, permissions, role, user.isset, user.payment_status, user.verification_status])

  useEffect(() => {
    check();
  }, [check])

  return (
    children
  )

}