import { faAngleDown, faAngleUp, faEdit, faUpload } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { alert, baseUrl, query, request } from '../../../utils';
import defaultProfilePicture from '../../../assets/image/default_profile_picture.png';
import { DashboardLayout } from '../../../components';
import { useUserStore } from '../../../store/user';
import { useAuthStore } from '../../../store/auth';
import { useCallback } from 'react';


export const Profile = () => {

  const [isEditingBio, setIsEditingBio] = useState(false)
  const [isEditingBankDetails, setIsEditingBankDetails] = useState(false)
  const [isEditingProfilePicture, setIsEditingProfilePicture] = useState(false)
  const [isEditingPassword, setIsEditingPassword] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const [showOPassword, setShowOPassword] = useState(false)
  const [showNPassword, setShowNPassword] = useState(false)
  const [showCPassword, setShowCPassword] = useState(false)
  const [profilePictureDisabled, setProfilePictureDisabled] = useState(false)
  const [bioDisabled, setBioDisabled] = useState(true)
  const [isSubmittingBio] = useState(false)
  const [bankDetailDisabled, setBankDetailDisabled] = useState(true)
  const [passwordDisabled, setPasswordDisabled] = useState(true)
  const [selectedNationality, setSelectedNationality] = useState('')
  const [showUpdatePassword, setShowUpdatePassword] = useState(false)
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const [nigerianBanks] = useState([
    "Access Bank Plc",
    "Fidelity Bank Plc",
    "First City Monument Bank Limited",
    "First Bank of Nigeria Limited",
    "Guaranty Trust Bank Plc",
    "Union Bank of Nigeria Plc",
    "United Bank for Africa Plc",
    "Zenith Bank Plc",
    "Citibank Nigeria Limited",
    "Ecobank Nigeria",
    "Heritage Bank Plc",
    "Keystone Bank Limited",
    "Polaris Bank Limited.",
    "Stanbic IBTC Bank Plc",
    "Standard Chartered",
    "Sterling Bank Plc",
    "Titan Trust Bank Limited",
    "Unity Bank Plc",
    "Wema Bank Plc",
    "Globus Bank Limited",
    "SunTrust Bank Nigeria Limited",
    "Providus Bank Limited",
    "Jaiz Bank Plc",
    "TAJBank Limited",
  ]);
  // const [favouriteCompetitionTeams, setFavouriteCompetitions] = useState([]);
  const [localCompetition, setLocalCompetition] = useState('');
  const [localTeams, setLocalTeams] = useState([]);
  const [foreignCompetition, setForeignCompetition] = useState('');
  const [foreignTeams, setForeignTeams] = useState([]);
  const [internationalCompetition, setInternationalCompetition] = useState('');
  // const [internationalTeams, setInternationalTeams] = useState([]);
  const [continentalCompetition, setContinentalCompetition] = useState('');
  const [continentalTeams, setContinentalTeams] = useState([]);
  const [worldCupCompetition, setWorldCupCompetition] = useState('');
  const [worldCupTeams, setWorldCupTeams] = useState([]);
  const [olympicCompetition, setOlympicCompetition] = useState('');
  const [olympicTeams, setOlympicTeams] = useState([]);
  const [myPackage, setMyPackage] = useState({});
  const [profile, setProfile] = useState({});
  const { user, setUser } = useUserStore()
  const { auth } = useAuthStore()

  let countries = useRef([])
  let competitions = useRef([])
  let internationalTeams = useRef([])

  const programIndexMap = useMemo(() => ({
    'Local': 1,
    'Foreign': 2,
    'International': 3,
    'Continental': 4,
    'World Cup': 5,
    'Olympic': 6
  }), [])

  const preview = (input, dest) => {
    if (input.target.files && input.target.files[0]) {
      let reader = new FileReader();
      reader.onload = function (e) {
        query(`#${dest}`)[0].setAttribute('src', e.target.result);
      }
      reader.readAsDataURL(input.target.files[0])
    }
  }

  const updateProfilePicture = async () => {
    setIsEditingProfilePicture(false)
    setProfilePictureDisabled(true)

    let body = new FormData();
    let profilepic = query('#profilePicture')[0].files[0];
    body.append('profile-picture', profilepic);

    let headers = new Headers();
    headers.append('AuthToken', auth.token);

    try {
      let resp = await request('/api/profile/update-profile-picture', 'POST', body, headers);
      if (resp.status === true) {
        setUser({ ...user, profile_picture: resp.data.path });
        setProfilePictureDisabled(false)
        alert('Profile picture updated', 'success')
      }
    } catch (error) {
      alert('Network Error', 'error')
    }

  }

  const updatePassword = async (e) => {
    e.preventDefault();
    let updatePasswordForm = query('#updatePasswordForm')[0];

    if (!updatePasswordForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updatePasswordForm.classList.add('was-validated')
    } else {

      setPasswordDisabled(true)

      let opassword = query('#profileOldPassword')[0];
      let npassword = query('#profileNewPassword')[0];
      let cpassword = query('#profileConfirmNewPassword')[0];
      let body = new FormData();
      body.append('opassword', opassword.value)
      body.append('npassword', npassword.value)
      body.append('cpassword', cpassword.value)

      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      try {
        let resp = await request('/api/profile/update-password', 'POST', body, headers);
        if (resp.status === true) {
          alert('Password updated successfully', 'success');
          opassword.value = ''; npassword.value = ''; cpassword.value = '';
          setIsEditingPassword(false)
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          setPasswordDisabled(false)
        }
      } catch (error) {
        alert('Network Error', 'error')
        setPasswordDisabled(false)
      }
    }
  }

  const updateBankDetails = async (e) => {
    e.preventDefault();
    let updateBankDetailForm = query('#updateBankDetailForm')[0];

    if (!updateBankDetailForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updateBankDetailForm.classList.add('was-validated')
    } else {

      setBankDetailDisabled(true)

      let accountnumber = query('#profileAccountnumber')[0];
      let accountname = query('#profileAccountname')[0];
      let bankname = query('#profileBankname')[0];
      let password = query('#profilePassword')[0];

      let body = new FormData();
      body.append('accountnumber', accountnumber.value)
      body.append('accountname', accountname.value)
      body.append('bankname', bankname.value)
      body.append('password', password.value)

      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      try {
        let resp = await request('/api/profile/update-bank-details', 'POST', body, headers);
        if (resp.status === true) {
          alert('Bank details updated successfully', 'success');
          setUser({
            ...user,
            accountname: accountname.value,
            accountnumber: accountnumber.value,
            bankname: bankname.value
          })
          password.value = '';
          setIsEditingBankDetails(false)
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          setBankDetailDisabled(false)
        }
      } catch (error) {
        alert('Network Error', 'error', false)
        setBankDetailDisabled(false)
      }
    }
  }

  const updateProfileBioConfirm = async (e) => {
    e.preventDefault();
    let favourite_team_local, favourite_team_foreign, favourite_team_international, favourite_team_continental, favourite_team_worldcup, favourite_team_olympic = "";

    favourite_team_local = query('#profileFavTeamLocal')[0].value;

    if (programIndexMap[myPackage.slot_program] > 1) { favourite_team_foreign = query('#profileFavTeamForeign')[0].value; }
    if (programIndexMap[myPackage.slot_program] > 2) { favourite_team_international = query('#profileFavTeamInternational')[0].value; }
    if (programIndexMap[myPackage.slot_program] > 3) { favourite_team_continental = query('#profileFavTeamContinental')[0].value; }
    if (programIndexMap[myPackage.slot_program] > 4) { favourite_team_worldcup = query('#profileFavTeamWorldcup')[0].value; }
    if (programIndexMap[myPackage.slot_program] > 5) { favourite_team_olympic = query('#profileFavTeamOlympic')[0].value; }

    let profile = {
      email: user.email,
      firstname: query('#profileFirstname')[0].value,
      lastname: query('#profileLastname')[0].value,
      middlename: query('#profileMiddlename')[0].value,
      telephone: query('#profileTelephone')[0].value,
      nationality: query('#profileNationality')[0].value,
      occupation: query('#profileOccupation')[0].value,
      residential_address: query('#profileResidentialAddress')[0].value,
      nextofkin_name: query('#profileNOKName')[0].value,
      nextofkin_telephone: query('#profileNOKTelephone')[0].value,
      nextofkin_residential_address: query('#profileNOKResidentialAddress')[0].value,
      favourite_sport: query('#profileFavSport')[0].value,
      favourite_team_local: favourite_team_local,
      favourite_team_foreign: favourite_team_foreign,
      favourite_team_international: favourite_team_international,
      favourite_team_continental: favourite_team_continental,
      favourite_team_worldcup: favourite_team_worldcup,
      favourite_team_olympic: favourite_team_olympic
    }

    setProfile(profile);
    query("#confirmUpdateModalTriggerBtn")[0].click();
  }

  const updateProfileBio = async (e) => {
    e.preventDefault();
    let updateBioForm = query('#updateBioForm')[0];

    query("#confirmUpdateModalDismissBtn")[0].click();
    if (!updateBioForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updateBioForm.classList.add('was-validated')
    } else {
      setBioDisabled(true)

      let firstname = query('#profileFirstname')[0].value
      let lastname = query('#profileLastname')[0].value
      let middlename = query('#profileMiddlename')[0].value
      let telephone = query('#profileTelephone')[0].value
      let nationality = query('#profileNationality')[0].value
      let occupation = query('#profileOccupation')[0].value
      let residential_address = query('#profileResidentialAddress')[0].value
      let nextofkin_name = query('#profileNOKName')[0].value
      let nextofkin_telephone = query('#profileNOKTelephone')[0].value
      let nextofkin_residential_address = query('#profileNOKResidentialAddress')[0].value
      let favourite_sport = query('#profileFavSport')[0].value

      let favourite_team_local, favourite_team_foreign, favourite_team_international, favourite_team_continental, favourite_team_worldcup, favourite_team_olympic = null;
      favourite_team_local = query('#profileFavTeamLocal')[0].value;
      if (programIndexMap[myPackage.slot_program] > 1) { favourite_team_foreign = query('#profileFavTeamForeign')[0].value; }
      if (programIndexMap[myPackage.slot_program] > 2) { favourite_team_international = query('#profileFavTeamInternational')[0].value; }
      if (programIndexMap[myPackage.slot_program] > 3) { favourite_team_continental = query('#profileFavTeamContinental')[0].value; }
      if (programIndexMap[myPackage.slot_program] > 4) { favourite_team_worldcup = query('#profileFavTeamWorldcup')[0].value; }
      if (programIndexMap[myPackage.slot_program] > 5) { favourite_team_olympic = query('#profileFavTeamOlympic')[0].value; }

      let body = new FormData();
      body.append('firstname', firstname);
      body.append('lastname', lastname);
      body.append('middlename', middlename);
      body.append('telephone', telephone);
      body.append('nationality', nationality);
      body.append('occupation', occupation);
      body.append('residential_address', residential_address);
      body.append('nextofkin_name', nextofkin_name);
      body.append('nextofkin_telephone', nextofkin_telephone);
      body.append('nextofkin_residential_address', nextofkin_residential_address);
      body.append('favourite_sport', favourite_sport);
      body.append('favourite_team_local', favourite_team_local ?? '');
      if (programIndexMap[myPackage.slot_program] > 1) { body.append('favourite_team_foreign', favourite_team_foreign ?? ''); }
      if (programIndexMap[myPackage.slot_program] > 2) { body.append('favourite_team_international', favourite_team_international ?? ''); }
      if (programIndexMap[myPackage.slot_program] > 3) { body.append('favourite_team_continental', favourite_team_continental ?? ''); }
      if (programIndexMap[myPackage.slot_program] > 4) { body.append('favourite_team_worldcup', favourite_team_worldcup ?? ''); }
      if (programIndexMap[myPackage.slot_program] > 5) { body.append('favourite_team_olympic', favourite_team_olympic ?? ''); }

      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      try {
        let resp = await request('/api/profile/update-bio', 'POST', body, headers);
        if (resp.status === true) {
          alert('Profile updated successfully', 'success');
          setUser({
            ...user,
            firstname: firstname,
            lastname: lastname,
            middlename: middlename,
            telephone: telephone,
            nationality: nationality,
            occupation: occupation,
            residential_address: residential_address,
            nextofkin_name: nextofkin_name,
            nextofkin_telephone: nextofkin_telephone,
            nextofkin_residential_address: nextofkin_residential_address,
            favourite_sport: favourite_sport,
            favourite_team_local: favourite_team_local,
            favourite_team_foreign: programIndexMap[myPackage.slot_program] > 1 ? favourite_team_foreign : null,
            favourite_team_international: programIndexMap[myPackage.slot_program] > 2 ? favourite_team_international : null,
            favourite_team_continental: programIndexMap[myPackage.slot_program] > 3 ? favourite_team_continental : null,
            favourite_team_worldcup: programIndexMap[myPackage.slot_program] > 4 ? favourite_team_worldcup : null,
            favourite_team_olympic: programIndexMap[myPackage.slot_program] > 5 ? favourite_team_olympic : null
          })

          setIsEditingBio(false)
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          setBioDisabled(false)
        }
      } catch (error) {
        console.log(error);
        alert('Network Error', 'error', false)
        setBioDisabled(false)
      }
    }
  }

  const setNationality = async (e) => {
    let selectedNation = countries.current.find((country) => { return country.country === e.target.value; });
    setSelectedNationality(selectedNation.country)
    setCompetitionsAndQualifiedTeams();
  }

  const handleInternationalCompetitionChange = (competition) => {
    competition = competition.substr(competition.indexOf('-') + 2);
    setInternationalCompetition(competition)
  }

  const confirmUpdateModal = () => {
    return (
      <div>
        <button className="d-none" id="confirmUpdateModalTriggerBtn" data-bs-toggle="modal" data-bs-target="#confirmUpdateModal"></button>
        <div className="modal fade" id="confirmUpdateModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="confirmUpdateModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="confirmUpdateModalLabel">Confirm Update</h5>
                <button id="confirmUpdateModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={updateProfileBio}>
                <div className="modal-body">
                  <div className='mb-3'>
                    <h3>Bio Info</h3>

                    <div className='row'>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <h5 className='mt-4'>Personal Info</h5>
                        <div className='mb-1'><small><b>FIRSTNAME:</b></small> {profile.firstname}</div>
                        <div className='mb-1'><small><b>LASTNAME:</b></small> {profile.lastname}</div>
                        <div className='mb-1'><small><b>MIDDLENAME:</b></small> {profile.middlename}</div>
                        <div className='mb-1'><small><b>EMAIL:</b></small> {profile.email}</div>
                        <div className='mb-1'><small><b>TELEPHONE:</b></small> {profile.telephone}</div>
                        <div className='mb-1'><small><b>OCCUPATION:</b></small> {profile.occupation}</div>
                        <div className='mb-1'><small><b>NATIONALITY:</b></small> {profile.nationality}</div>
                        <div className='mb-1'><small><b>RESIDENTIAL ADDRESS:</b></small> {profile.residential_address}</div>
                      </div>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <h5 className='mt-4'>Next of Kin Info</h5>
                        <div className='mb-1'><small><b>NEXT OF KIN FULLNAME:</b></small> {profile.nextofkin_name}</div>
                        <div className='mb-1'><small><b>NEXT OF KIN TELEPHONE:</b></small> {profile.nextofkin_telephone}</div>
                        <div className='mb-1'><small><b>NEXT OF KIN RESIDENTIAL ADDRESS:</b></small> {profile.nextofkin_residential_address}</div>
                      </div>
                    </div>

                  </div>

                  <hr />

                  <div className='mb-3'>
                    <h3>FAVOURITE SPORT &amp; TEAMS</h3>
                    <h5 className='mt-4'>FAVOURITE SPORT: {profile.favourite_sport}</h5>

                    <div className='row'>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <div className='mb-1'><small><b>LOCAL TEAM:</b></small> {profile.favourite_team_local}</div>
                        <div className='mb-1'><small><b>FOREIGN TEAM:</b></small> {profile.favourite_team_foreign}</div>
                        <div className='mb-1'><small><b>INTERNATIONAL TEAM:</b></small> {profile.favourite_team_international}</div>
                      </div>
                      <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                        <div className='mb-1'><small><b>CONTINENTAL TEAM:</b></small> {profile.favourite_team_continental}</div>
                        <div className='mb-1'><small><b>WORLD CUP TEAM:</b></small> {profile.favourite_team_worldcup}</div>
                        <div className='mb-1'><small><b>OLYMPIC TEAM:</b></small> {profile.favourite_team_olympic}</div>
                      </div>
                    </div>

                  </div>
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-muted px-5" data-bs-dismiss="modal" aria-label="Close">Cancel</button>
                  <button type="submit" className="btn btn-primary px-5">Confirm</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const renderInternationalCompetitionAndTeams = useCallback(() => {
    query('#profileFavTeamInternational')[0].innerHTML = "";
    internationalTeams.current.forEach((continent) => {
      let competition = continent.competition;
      let teams = JSON.parse(continent.qualified_teams);

      let teamsString = ``;
      teams.forEach((team, key) => (
        teamsString += `<option key=${key} value='${team} - ${competition}'>${team}</option>`
      ));

      query('#profileFavTeamInternational')[0].insertAdjacentHTML('beforeEnd',
        `<optgroup label="${competition}">
          ${teamsString}
        </optgroup>`
      );

    })
  }, [internationalTeams])

  const handleDialCodeChange = () => {
    let dial = query('#telDialCode')[0];
    let tel = query("#profileTelephone")[0];
    // clear old value
    let newTel = tel.value.toString().replace(/\+([0-9]{3}|[0-9]{2}) /, '');
    // compose new value
    tel.value = `${dial.value} ${newTel}`;
  }

  const setCompetitionsAndQualifiedTeams = useCallback(() => {
    let sport = query('#profileFavSport')[0].value ?? 'Football';
    let nation = selectedNationality !== '' ? selectedNationality : 'Nigeria';

    let continent = countries.current.find((country) => {
      return country.country === nation;
    }).continent;

    let local = competitions.current
      .find((competition) => {
        return competition.sport === sport && competition.region === nation;
      })

    if (local === undefined) {
      alert(`No ${sport} Competition for ${nation}`, 'warning', false);
      setLocalCompetition("No competition")
      setLocalTeams([])
    } else {
      setLocalCompetition(local.competition)
      setLocalTeams(JSON.parse(local.qualified_teams))
    };

    // if (programIndexMap[myPackage.slot_program] > 1) {
    let foreign = competitions.current
      .find((competition) => {
        return competition.sport === sport && competition.region === continent && competition.slotpackage === '2';
      })

    if (foreign === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'warning', false);
      setForeignCompetition("No competition")
      setForeignTeams([])
    } else {
      setForeignCompetition(foreign.competition)
      setForeignTeams(JSON.parse(foreign.qualified_teams))
    };
    // }

    // if (programIndexMap[myPackage.slot_program] > 2) {
    let international = competitions.current
      .filter((competition) => { return competition.sport === sport && competition.region !== continent && competition.slotpackage === '2'; })

    if (international === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'warning', false);
      setInternationalCompetition("No competition")
      internationalTeams.current = []
    } else {
      setInternationalCompetition(international[0].competition)
      internationalTeams.current = international
      renderInternationalCompetitionAndTeams()
    };
    // }

    // if (programIndexMap[myPackage.slot_program] > 3) {
    let continental = competitions.current
      .find((competition) => {
        return competition.sport === sport && competition.region === continent && competition.slotpackage === '4';
      });

    if (continental === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'warning', false);
      setContinentalCompetition("No competition")
      setContinentalTeams([])
    } else {
      setContinentalCompetition(continental.competition)
      setContinentalTeams(continental.qualified_teams)
    };
    // }

    // if (programIndexMap[myPackage.slot_program] > 4) {
    let worldcup = competitions.current
      .find((competition) => { return competition.sport === sport && competition.region === 'World Wide' && competition.slotpackage === '5'; })

    if (worldcup === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'warning', false);
      setWorldCupCompetition("No competition")
      setWorldCupTeams([])
    } else {
      setWorldCupCompetition(worldcup.competition)
      setWorldCupTeams(JSON.parse(worldcup.qualified_teams))
    };
    // }

    // if (programIndexMap[myPackage.slot_program] > 5) {
    let olympic = competitions.current
      .find((competition) => { return competition.sport === sport && competition.region === 'World Wide' && competition.slotpackage === '6'; });

    if (olympic === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'warning', false);
      setOlympicCompetition("No competition")
      setOlympicTeams([])
    } else {
      setOlympicCompetition(olympic.competition)
      setOlympicTeams(JSON.parse(olympic.qualified_teams))
    };
    // }

  }, [renderInternationalCompetitionAndTeams, selectedNationality, internationalTeams])

  const getPackage = useCallback(async () => {
    // get my slot program
    try {
      // if (myPackage !== {}) return
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/mypackage', 'GET', null, headers);

      if (resp.status === true) {
        setMyPackage(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  const getCountries = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      // countries
      let resp = await request('/api/getcountries', 'GET', null, headers);
      if (resp.status === true) {
        countries.current = resp.data
        // set default nationality of user
        setSelectedNationality(user.nationality ?? 'Nigeria')
        handleDialCodeChange()
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token, user.nationality])

  const getCompetitions = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      // competitions
      let resp = await request('/api/getcompetitions', 'GET', null, headers);
      if (resp.status === true) {
        competitions.current = resp.data;
        setCompetitionsAndQualifiedTeams();
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token, setCompetitionsAndQualifiedTeams])

  useEffect(() => {
    getPackage();
    if (pageIsLoading === false) return
    getCountries();
    getCompetitions(countries);

    setPageIsLoading(false)
    return () => { }
  }, [getCompetitions, getCountries, getPackage, pageIsLoading])

  return (
    <>
      <DashboardLayout page='/profile'>

        <section className='container my-3 px-3 text-muted'>

          <div className='d-flex justify-content-between text-dark'>
            <h1 className='fw-normal fs-2'>
              Profile
              <small className='fs-6 d-block fw-light'>{user.email} | <span title='Member ID'>{user.member_id ?? 'No Member ID'}</span></small>
            </h1>
            <div className='d-flex flex-column justify-content-center position-relative'>
              <label style={{ cursor: 'pointer' }} htmlFor='profilePicture'>
                <img id='profilePicturePreview' className='rounded-circle' width='60px' height='60px' src={user.profile_picture != null ? `${baseUrl}/${user.profile_picture}` : defaultProfilePicture} alt='' />
              </label>

              <input disabled={profilePictureDisabled} type="file" onChange={(e) => {
                preview(e, 'profilePicturePreview');
                setIsEditingProfilePicture(true)
              }} className='d-none' id='profilePicture' />

              {isEditingProfilePicture &&
                <div htmlFor='profilePicture' onClick={updateProfilePicture} className='position-absolute w-100 bottom-0 btn btn-sm btn-primary py-0 m-0 rounded' title='Upload profile picture'>
                  <FontAwesomeIcon className="text-light" icon={faUpload} size='sm' />
                </div>
              }
            </div>
          </div>
          <hr />

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div className='my-4 border rounded px-4 py-4'>

                  <div className='d-flex justify-content-between'>
                    <h3>Bio Info</h3>
                    <div>
                      <FontAwesomeIcon onClick={() => { setBioDisabled(!bioDisabled); setIsEditingBio(!isEditingBio) }} className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" icon={faEdit} />
                    </div>
                  </div>

                  <form id='updateBioForm' onSubmit={(e) => updateProfileBioConfirm(e)} noValidate={true} >

                    <h5 className='mt-4'>Personal Info</h5>

                    <div className='row'>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bioDisabled} className="form-control" id="profileFirstname" placeholder="firstname" required defaultValue={user.firstname} />
                        <label htmlFor="profileFirstname" className='ps-4'>First Name<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your first name.</div>
                      </div>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bioDisabled} className="form-control" id="profileLastname" placeholder="lastname" required defaultValue={user.lastname} />
                        <label htmlFor="profileLastname" className='ps-4'>Last Name<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your last name.</div>
                      </div>

                    </div>

                    <div className="form-floating mb-4">
                      <input type="tel" disabled={bioDisabled} className="form-control" id="profileMiddlename" placeholder="middlename" required defaultValue={user.middlename} />
                      <label htmlFor="profileMiddlename" className=''>Middle Name<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your middle name.</div>
                    </div>

                    <div className='row'>

                      <div className="col-lg-2 col-4 form-floating mb-4">
                        <select name="telDialCode" id="telDialCode" disabled={bioDisabled} className="form-control"
                          value={
                            countries.current.length > 0 && countries.current.find(country => country.country === selectedNationality).dial_code
                          }
                          onChange={handleDialCodeChange}>
                          {
                            countries.current.map((country, index) => (
                              <option key={index} value={country.dial_code}>{country.country} ({country.dial_code})</option>
                            ))
                          }
                        </select>
                        <label htmlFor="telDialCode" className='ps-4'>Dial Code<span className='text-danger ms-1'>*</span></label>
                      </div>


                      <div className="col-lg-4 col-8 form-floating mb-4">
                        <input type="tel" disabled={bioDisabled} className="form-control" id="profileTelephone" placeholder="telephone" required defaultValue={user.telephone} />
                        <label htmlFor="profileTelephone" className='ps-4'>Telephone<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your first name.</div>
                      </div>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bioDisabled} className="form-control" id="profileOccupation" placeholder="occupation" required defaultValue={user.occupation} />
                        <label htmlFor="profileOccupation" className='ps-4'>Occupation<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your occupation.</div>
                      </div>

                    </div>

                    <div className="form-floating mb-4">
                      <select disabled={bioDisabled} onChange={setNationality} className="form-select" id="profileNationality" placeholder="nationality" required
                        value={
                          countries.current.length > 0 && countries.current.find(country => country.country === selectedNationality).country
                        }
                      >
                        {
                          countries.current.map((country, key) => (
                            <option key={key} value={country.country}>{country.country}</option>
                          ))
                        }
                      </select>
                      <label htmlFor="profileNationality" className=''>Nationality<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please choose your nationality.</div>
                    </div>

                    <div className="form-floating mb-4">
                      <textarea className="form-control" disabled={bioDisabled} placeholder="address" id="profileResidentialAddress" style={{ height: '100px' }} defaultValue={user.residential_address} required />
                      <label htmlFor="profileResidentialAddress">Residential Address<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your residentail address.</div>
                    </div>

                    <h5 className='mt-4'>Next of Kin Info</h5>

                    <div className='row'>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bioDisabled} className="form-control" id="profileNOKName" placeholder="fullname" required defaultValue={user.nextofkin_name} />
                        <label htmlFor="profileNOKName" className='ps-4'>Next of Kin Name<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter the full name of your next of kin.</div>
                      </div>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="tel" disabled={bioDisabled} className="form-control" id="profileNOKTelephone" placeholder="telephone" required defaultValue={user.nextofkin_telephone} />
                        <label htmlFor="profileNOKTelephone" className='ps-4'>Next of Kin Telephone<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter the telephone number of your next of kin.</div>
                      </div>

                    </div>

                    <div className="form-floating mb-4">
                      <textarea className="form-control" disabled={bioDisabled} placeholder="Leave a comment here" id="profileNOKResidentialAddress" style={{ height: '100px' }} defaultValue={user.nextofkin_residential_address} required />
                      <label htmlFor="profileNOKResidentialAddress">Next of Kin Residential Address<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your next of kin residential address.</div>
                    </div>

                    <h5 className='mt-4'>
                      {/* <img width='20px' className='me-2' src='https://apiv2.apifootball.com/badges/2612_everton.png' alt='team badge' /> */}
                      Your Favourite Team Info
                      <br />
                      <small className='fw-normal' style={{ fontSize: '14px' }}>
                        <span className='text-danger ms-1'>*</span>
                        Note that you cannot change your favourite sport or favourite team after choosing now.
                      </small>
                    </h5>

                    <div className="form-floating mb-4">
                      {
                        user.favourite_sport === null ?
                          <select disabled={bioDisabled} className="form-select" id="profileFavSport" placeholder="sport" required defaultValue={user.favourite_sport}>
                            {
                              [
                                "Football",
                                // "American Football", "Baseball", "Basketball", "Car Racing", "Boxing", "Swimming", "Tennis", "Golf", "Racing", "Hockey"
                              ].map((sport, key) => (
                                <option key={key} value={sport}>{sport}</option>
                              ))
                            }
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavSport" defaultValue={user.favourite_sport} />
                      }
                      <label htmlFor="profileFavSport" className=''>Favourite Sport<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite sport.</div>
                    </div>

                    {/* This should be dependent on the slot chosen, sport chosen, and the competition available for the sport in that locality (slot chosen decide the locality) */}

                    <div className='row'>

                      <div className="col-lg-6 form-floating mb-4">
                        {
                          user.favourite_team_local === null ?
                            < select readOnly={true} disabled={bioDisabled} className="form-select" id="profileFavTeamLocal" placeholder="sport" required defaultValue={user.favourite_team_local}>
                              {
                                localTeams.map((team, key) => (
                                  <option key={key} value={`${team} - ${localCompetition}`}>{team}</option>
                                ))
                              }
                            </select>
                            :
                            <input type="text" readOnly={true} className="form-control" id="profileFavTeamLocal" defaultValue={user.favourite_team_local} />
                        }
                        <label htmlFor="profileFavTeamLocal" className='ps-4'>Favourite Local Team<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your favourite local team.</div>
                        <span className='text-primary fs-6'>{localCompetition}</span>
                      </div>


                      {programIndexMap[myPackage.slot_program] > 1 &&
                        <div className="col-lg-6 form-floating mb-4">
                          {
                            user.favourite_team_foreign === null ?
                              <select disabled={bioDisabled} className="form-select" id="profileFavTeamForeign" placeholder="sport" required defaultValue={user.favourite_team_foreign}>
                                {
                                  foreignTeams.map((team, key) => (
                                    <option key={key} value={`${team} - ${foreignCompetition}`}>{team}</option>
                                  ))
                                }
                              </select>
                              :
                              <input type="text" readOnly={true} className="form-control" id="profileFavTeamForeign" defaultValue={user.favourite_team_foreign} />
                          }
                          <label htmlFor="profileFavTeamForeign" className='ps-4'>Favourite Foreign Team<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your favourite foreign team.</div>
                          <span className='text-primary fs-6'>{foreignCompetition}</span>
                        </div>
                      }
                    </div>

                    <div className='row'>

                      {programIndexMap[myPackage.slot_program] > 2 &&
                        <div className="col-lg-6 form-floating mb-4">
                          {
                            user.favourite_team_international === null ?
                              <select disabled={bioDisabled} className="form-select" id="profileFavTeamInternational" placeholder="sport" required defaultValue={user.favourite_team_international} onChange={(e) => handleInternationalCompetitionChange(e.target.value)}>
                                {/* {renderInternationalCompetitionAndTeams()} */}
                              </select>
                              :
                              <input type="text" readOnly={true} className="form-control" id="profileFavTeamInternational" defaultValue={user.favourite_team_international} />
                          }
                          <label htmlFor="profileFavTeamInternational" className='ps-4'>Favourite International Team<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your favourite international team.</div>
                          <span className='text-primary fs-6'>{internationalCompetition}</span>
                        </div>
                      }

                      {programIndexMap[myPackage.slot_program] > 3 &&
                        <div className="col-lg-6 form-floating mb-4">
                          {
                            user.favourite_team_continental === null ?
                              <select disabled={bioDisabled} className="form-select" id="profileFavTeamContinental" placeholder="sport" required defaultValue={user.favourite_team_continental}>
                                {
                                  continentalTeams.map((team, key) => (
                                    <option key={key} value={`${team} - ${continentalCompetition}`}>{team}</option>
                                  ))
                                }
                              </select>
                              :
                              <input type="text" readOnly={true} className="form-control" id="profileFavTeamContinental" defaultValue={user.favourite_team_continental} />
                          }
                          <label htmlFor="profileFavTeamContinental" className='ps-4'>Favourite Continental Team<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your favourite continental team.</div>
                          <span className='text-primary fs-6'>{continentalCompetition}</span>
                        </div>
                      }

                    </div>

                    <div className='row'>

                      {programIndexMap[myPackage.slot_program] > 4 &&
                        <div className="col-lg-6 form-floating mb-4">
                          {
                            user.favourite_team_worldcup === null ?
                              <select disabled={bioDisabled} className="form-select" id="profileFavTeamWorldcup" placeholder="sport" required defaultValue={user.favourite_team_worldcup}>
                                {
                                  worldCupTeams.map((team, key) => (
                                    <option key={key} value={`${team} - ${worldCupCompetition}`}>{team}</option>
                                  ))
                                }
                              </select>
                              :
                              <input type="text" readOnly={true} className="form-control" id="profileFavTeamWorldcup" defaultValue={user.favourite_team_worldcup} />
                          }
                          <label htmlFor="profileFavTeamWorldcup" className='ps-4'>Favourite World Cup Team<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your favourite world cup team.</div>
                          <span className='text-primary fs-6'>{worldCupCompetition}</span>
                        </div>
                      }

                      {programIndexMap[myPackage.slot_program] > 5 &&
                        <div className="col-lg-6 form-floating mb-4">
                          {
                            user.favourite_team_olympic === null ?
                              <select disabled={bioDisabled} className="form-select" id="profileFavTeamOlympic" placeholder="sport" required defaultValue={user.favourite_team_olympic}>
                                {
                                  olympicTeams.map((team, key) => (
                                    <option key={key} value={`${team} - ${olympicCompetition}`}>{team}</option>
                                  ))
                                }
                              </select>
                              :
                              <input type="text" readOnly={true} className="form-control" id="profileFavTeamOlympic" defaultValue={user.favourite_team_olympic} />
                          }
                          <label htmlFor="profileFavTeamOlympic" className='ps-4'>Favourite Olympic Team<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your favourite olympic team.</div>
                          <span className='text-primary fs-6'>{olympicCompetition}</span>
                        </div>
                      }

                    </div>

                    {
                      isEditingBio &&
                      <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={bioDisabled} type="submit">
                        {
                          isSubmittingBio === true
                            ? <div>
                              <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                              <span className='ms-2 align-middle'>Updating your profile...</span>
                            </div>
                            : <span className='ms-2 align-middle'>Update</span>}
                      </button>
                    }

                  </form>

                </div>

                <div className='my-4 border rounded px-4 py-4'>

                  <div className='d-flex justify-content-between'>
                    <h3>Bank Details</h3>
                    <div>
                      <FontAwesomeIcon className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" onClick={() => {
                        setBankDetailDisabled(!bankDetailDisabled);
                        setIsEditingBankDetails(!isEditingBankDetails)
                      }} icon={faEdit} ></FontAwesomeIcon>
                    </div>
                  </div>

                  <form id='updateBankDetailForm' onSubmit={(e) => updateBankDetails(e)} noValidate={true} >

                    <div className='row'>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bankDetailDisabled} className="form-control" id="profileAccountnumber" placeholder="accountnumber" required defaultValue={user.accountnumber} />
                        <label htmlFor="profileAccountnumber" className='ps-4'>Account Number<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your bank account number.</div>
                      </div>

                      <div className="col-lg-6 form-floating mb-4">
                        <input type="text" disabled={bankDetailDisabled} className="form-control" id="profileAccountname" placeholder="accountname" required defaultValue={user.accountname} />
                        <label htmlFor="profileAccountname" className='ps-4'>Account Name<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your bank account name.</div>
                      </div>

                    </div>

                    <div className="form-floating mb-4">
                      <select disabled={bankDetailDisabled} className="form-select" id="profileBankname" placeholder="bankname" required defaultValue={user.bankname ?? ''}>
                        {
                          nigerianBanks.sort().map((bank, key) => (
                            <option key={key} value={bank}>{bank}</option>
                          ))
                        }
                      </select>
                      <label htmlFor="profileBankname" className=''>Bank Name<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please select your bank.</div>
                    </div>

                    <div className="form-floating mb-1 clearfix">
                      <input type={showPassword === true ? "text" : "password"} autoComplete='off' disabled={bankDetailDisabled} className="form-control" id="profilePassword" placeholder='******' required />
                      <label htmlFor="profilePassword">Enter your login password<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your password.</div>
                      <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                        setShowPassword(!showPassword);
                      }
                      }>{showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                    </div>

                    {
                      isEditingBankDetails &&
                      <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={bankDetailDisabled} type="submit">
                        {
                          isSubmittingBio === true
                            ? <div>
                              <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                              <span className='ms-2 align-middle'>Updating your profile...</span>
                            </div>
                            : <span className='ms-2 align-middle'>Update</span>}
                      </button>
                    }

                  </form>

                </div>

                <div className="d-flex justify-content-end">
                  <div className="fw-500 btn btn-muted text-danger mb-3" onClick={() => {
                    setShowUpdatePassword(!showUpdatePassword);
                  }
                  }>Change Your Password{showUpdatePassword === true ? <FontAwesomeIcon className="mx-1" icon={faAngleUp}></FontAwesomeIcon> : <FontAwesomeIcon className="mx-1" icon={faAngleDown}></FontAwesomeIcon>}
                  </div>
                </div>

                {showUpdatePassword &&
                  <div className='mt-1 mb-4 border border-danger rounded px-4 py-4'>

                    <div className='d-flex justify-content-between'>
                      <h3>Update Password</h3>
                      <div>
                        <FontAwesomeIcon className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" onClick={() => { setPasswordDisabled(!passwordDisabled); setIsEditingPassword(!isEditingPassword) }} icon={faEdit} ></FontAwesomeIcon>
                      </div>
                    </div>

                    <form id='updatePasswordForm' onSubmit={(e) => updatePassword(e)} noValidate={true} >

                      <div className="form-floating mb-1 clearfix">
                        <input type={showOPassword === true ? "text" : "password"} autoComplete='off' disabled={passwordDisabled} className="form-control" id="profileOldPassword" placeholder='******' required />
                        <label htmlFor="profileOldPassword">Old Password<span className='text-danger ms-1'>*</span></label>
                        <div className="invalid-feedback">Please enter your old password.</div>
                        <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                          setShowOPassword(!showOPassword)
                        }
                        }>{showOPassword === true ? 'Hide Password' : 'Show Password'}</div>
                      </div>

                      <div className='row'>

                        <div className="col-lg-6 form-floating mb-1 clearfix">
                          <input type={showNPassword === true ? "text" : "password"} autoComplete='off' disabled={passwordDisabled} className="form-control" id="profileNewPassword" placeholder='******' required />
                          <label htmlFor="profileNewPassword" className='ps-4'>New Password<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please enter your new password.</div>
                          <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                            setShowNPassword(!showNPassword)
                          }
                          }>{showNPassword === true ? 'Hide Password' : 'Show Password'}</div>
                        </div>

                        <div className="col-lg-6 form-floating mb-1 clearfix">
                          <input type={showCPassword === true ? "text" : "password"} autoComplete='off' disabled={passwordDisabled} className="form-control" id="profileConfirmNewPassword" placeholder='******' required />
                          <label htmlFor="profileConfirmNewPassword" className='ps-4'>Confirm New Password<span className='text-danger ms-1'>*</span></label>
                          <div className="invalid-feedback">Please confirm your new password.</div>
                          <div className="fw-500 btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                            setShowCPassword(!showCPassword)
                          }
                          }>{showCPassword === true ? 'Hide Password' : 'Show Password'}</div>
                        </div>

                      </div>

                      {
                        isEditingPassword &&
                        <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={passwordDisabled} type="submit">
                          {
                            passwordDisabled === true
                              ? <div>
                                <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                                <span className='ms-2 align-middle'>Updating your password...</span>
                              </div>
                              : <span className='ms-2 align-middle'>Update</span>}
                        </button>
                      }

                    </form>

                  </div>
                }
              </div>
          }
        </section>

        {confirmUpdateModal()}
      </DashboardLayout >
    </>
  )
}
