import { faServer } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { formatMoney, formatNumber, request } from '../../../utils';
import { useAuthStore } from '../../../store/auth';
import { DashboardLayout } from '../../../components';
import { useCallback } from 'react';

export const Slots = () => {

  // state = {
  const [referralLevels, setReferralLevels] = useState([])
  const [myPackage, setMyPackage] = useState({})
  const [mySlots, setMySlots] = useState([])
  const [paymentRequired, setPaymentRequired] = useState(false)
  const [pageIsLoading, setPageIsLoading] = useState(true)
  const { auth } = useAuthStore()

  const getReferralLevels = async () => {
    try {
      let resp = await request('/api/referrallevels');
      let referrallevels = resp.status === true ? resp.data : [];
      setReferralLevels(referrallevels);
    } catch (error) {
      console.log(error);
    }
  }

  const getMyPackageAndSlots = useCallback(async () => {
    // get my package and slots
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);
      let resp = await request('/api/mypackage', 'GET', null, headers);

      if (resp.status === true) {
        setMyPackage(resp.data);
        if (myPackage.status === 'pending') {
          setPaymentRequired(true);
        }

        let slotsresp = await request('/api/myslots', 'GET', null, headers);
        if (slotsresp.status === true) {
          setMySlots(slotsresp.data);
        }
      }
    } catch (error) {
      // alert('Error retrieving slot')
      console.log(error);
    }
  }, [auth.token, myPackage.status])

  useEffect(() => {
    getReferralLevels()
    getMyPackageAndSlots()
    setPageIsLoading(false)
  }, [getMyPackageAndSlots])

  const renderSlots = (level) => {

    // do not render manager
    if (level.rank === "Manager") return;

    let slotsInThisLevel = mySlots.filter((slot) => { return slot.referral_level === level.rank });

    return (
      <div className='text-muted mt-4'>
        <h4 className='ms-lg-2 ms-2'>{level.rank}</h4>
        {
          slotsInThisLevel.map((slot, index) => (

            <div key={index} className='mb-3 rounded border border-light shadow px-lg-4 px-3 py-lg-5 py-3'>
              <div className='d-flex justify-content-between align-items-top'>
                <div className='fw-bold text-primary'>{formatNumber(slot.referrals_required)} referrals required{slot.status === 'completed' && ' '}</div>
                <div className='' style={{ fontSize: '14px' }}>
                  {slot.referrals_acquired}/{slot.referrals_required}
                </div>
              </div>
              <div className="progress mt-3">
                <div className={`progress-bar progress-bar-striped ${slot.status === 'pending' ? 'bg-muted' : 'bg-primary'} ${slot.status === 'active' && 'progress-bar-animated'}`} role="progressbar" style={{ width: `${Math.ceil(((slot.referrals_acquired) / slot.referrals_required) * 100)}%` }}>
                  {Math.floor((slot.referrals_acquired / slot.referrals_required) * 100)}%
                </div>
              </div>
              <div className='d-flex justify-content-between '>
                <div className='fs-5'>{formatMoney(level.benefits[(myPackage.no_slots - 1)].cash, 0)}</div>
                <div style={{ fontSize: '14px' }}>{slot.status}</div>
              </div>
            </div>

          ))
        }
      </div>
    )
  }

  return (
    <>
      <DashboardLayout page='/slots'>
        <section className='container my-3 px-3'>

          <div className='mb-2'>
            <h1 className='fw-normal fs-2'>Referrals</h1>
            <hr className='my-0' />
            <div className='fw-normal d-flex align-items-center py-0 my-0 fs-6' style={{ height: '30px' }}>
              <FontAwesomeIcon icon={faServer} className='text-primary me-1'></FontAwesomeIcon>
              <div className=''>{myPackage.slot_program} Program</div>
              {paymentRequired && <span className='text-muted fs-1 mx-1'></span>}
              {paymentRequired ? <Link to={'/payment-invoice'} className='text-primary btn p-0 m-0'>Activate your package</Link> : <></>}
            </div>
          </div>

          {
            pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div>
                  {
                    referralLevels.map((level, index) => (
                      <div key={index}>
                        {renderSlots(level)}
                      </div>
                    ))
                  }
                </div>
              </div>
          }

        </section>
      </DashboardLayout>
    </>
  );
}
