import React, { useEffect, useState, useCallback } from 'react';
import paystack from '../../../assets/image/paystack.png';
import flutterwave from '../../../assets/image/flutterwave.png';
import { alert, appUrl, formatDate, formatMoney, query, request } from '../../../utils';
import { usePaystackPayment } from 'react-paystack';
import { useAuthStore } from '../../../store/auth';
import { useUserStore } from '../../../store/user';
import { DashboardTopBar } from '../../../components';
import { useHistory } from 'react-router-dom';
import { Helmet } from 'react-helmet';


export const PayInvoice = () => {

  const [invoice, setInvoice] = useState({})
  const [noUnpaidInvoice, setNoUnpaidInvoice] = useState(false)
  const [pageIsLoading, setPageIsLoading] = useState(true);
  const { auth } = useAuthStore()
  const { user } = useUserStore()
  const history = useHistory()
  let token = '';

  const getUnpaidInvoice = useCallback(async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', auth.token);

      let resp = await request('/api/invoice/unpaid', 'GET', null, headers);
      if (resp.status === true) {
        setInvoice(resp.data)
      } else {
        setNoUnpaidInvoice(true)
      }
    } catch (error) {
      console.log(error);
    }
  }, [auth.token])

  // you can call this function anything
  function onPaystackSuccess(reference) {
    // Implementation for whatever you want to do with reference and after success call.
    verifyPayment(reference.trxref, token);
  };

  // you can call this function anything
  function onPaystackClose() {
    // implementation for  whatever you want to do when the Paystack dialog closed.
    console.log('closed')
    query("#loadingDiv")[0].classList.add('d-none');
  }

  async function initiatePayment(reference, token, invoice, service) {
    let body = new FormData();
    body.append('reference', reference);
    body.append('invoicenumber', invoice);
    body.append('service', service);

    let headers = new Headers();
    headers.append('AuthToken', token);

    try {
      let resp = await request('/api/payment/initiate', 'POST', body, headers);
      if (resp.status === true) {
        return true;
      } else {
        return false;
      }
    } catch (error) {
      alert('Network Error', 'error');
      return false;
    }
  }

  async function verifyPayment(reference, token) {
    alert('verifying payment...', 'light');

    let body = new FormData();
    body.append('reference', reference);

    let headers = new Headers();
    headers.append('AuthToken', token);

    try {
      let resp = await request('/api/payment/verify', 'POST', body, headers);
      if (resp.status === true) {
        alert(resp.message, 'success');
        query("#loadingDiv")[0].classList.add('d-none');
        console.log(resp.data)
        // log the user out
        localStorage.clear();
        history.replace('/login');
      } else {
        query("#loadingDiv")[0].classList.add('d-none');
        alert(resp.message, 'error');
        return false;
      }
    } catch (error) {
      query("#loadingDiv")[0].classList.add('d-none');
      alert('Network Error', 'error');
      return false;
    }
  }

  function PayWithPaystack({ email, invoice, amount, authtoken }) {
    token = authtoken;
    // role = authrole;
    let reference = `${invoice}-${Math.floor(Math.random() * 99999)}`;

    let config = {
      reference: reference,
      email: email.toString(),
      amount: amount * 100, // convert the naira to kobo
      publicKey: 'pk_test_579a731077fa2885e1119ad97a783bca3c66fb8d',
    };

    const initializePayment = usePaystackPayment(config);
    return (
      <div>
        <button className='btn btn-muted w-100 h-100 border mt-1' title="Pay with Paystack"
          disabled={false}
          onClick={async (e) => {
            try {
              console.log('Button Clicked...', e.target)
              query("#loadingDiv")[0].classList.remove('d-none');
              e.target.disabled = true;
              let initiate = await initiatePayment(reference, token, invoice, 'Paystack');
              if (initiate === true) {
                initializePayment(onPaystackSuccess, onPaystackClose);
              } else {
                query("#loadingDiv")[0].classList.add('d-none');
                alert('Could not initate Paystack service.', 'error');
              }
            } catch (error) {
              console.log(error);
              query("#loadingDiv")[0].classList.add('d-none');
              alert('Could not initate Paystack service.', 'error');
            }
          }}>
          <img src={paystack} className='img' width={200} alt="Pay with Paystack" />
        </button>
      </div>
    );
  };

  const PayWithFlutterwave = () => {
    // const initializePayment = usePaystackPayment();
    return (
      <div>
        <button className='btn btn-muted w-100 h-100 border mt-1 pt-2 pb-2' title="Pay with Flutterwave" disabled={true} onClick={(e) => {
          try {
            console.log('Button Clicked...', e.target)
            query("#loadingDiv")[0].classList.remove('d-none');
            // e.target.disabled = true;
            return;
            // initializePayment(onSuccess, onClose)
          } catch (error) {
            console.log(error);
            alert('Could not initate Flutterwave service.', 'error');
          }
        }}>
          <img src={flutterwave} className='img' width={200} alt="Pay with Flutterwave" />
        </button>
      </div>
    );
  };

  useEffect(() => {
    getUnpaidInvoice()
    setPageIsLoading(false)
  }, [getUnpaidInvoice])

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sports Fans Club | Payment Invoice</title>
        <link rel="canonical" href={`${appUrl}/payment-invoice`} />
        <meta name="description" content={"Sports Fans Club is a platform that creates the opportunity for individuals to become true fans of their respective sport teams."} />
      </Helmet>

      <DashboardTopBar></DashboardTopBar>
      <section className='container my-3 px-3 text-muted'>

        <div className='mb-3 text-dark'>
          <h1>Payment Invoice</h1>
          <hr className='my-0' />
        </div>
        {
          pageIsLoading === true ?
            <div id="loadingDiv">
              <div className="h-100 d-flex justify-content-center align-items-center text-muted">
                <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                <span className='ms-2 align-middle'>loading...</span>
              </div>
            </div>
            :
            <div>
              <div className='mb-3 rounded border border-light shadow px-lg-4 px-3 pt-lg-5 pt-3 pb-5'>

                {
                  noUnpaidInvoice === true ?
                    <>
                      <div className='d-flex justify-content-center'>
                        <div className='fs-3'>
                          No Unpaid Invoice
                        </div>
                      </div>
                    </>
                    :
                    <>
                      <div className='d-flex justify-content-between align-items-center'>
                        <div className='fs-2 text-primary'>Invoice #{invoice.invoice_number}</div>
                        <div>
                          {formatDate(invoice.created_at)} <br />
                          <div style={{ fontSize: '14px' }} className={invoice.status === 'unpaid' ? 'text-danger' : ''}>{invoice.status}</div>
                        </div>
                      </div>
                      <div>
                        <div>
                          <div className='fs-6'>{invoice.invoice_description}</div>
                          <div className='fs-6 mt-1 lh-0'>
                            Total:<span className='ms-1 fs-2'>{formatMoney(invoice.amount_due, 0)}</span>
                          </div>
                        </div>
                      </div>

                      <div className='mt-lg-3'>
                        <hr />
                        <span>Complete payment with:</span>

                        <div className='row mt-2'>
                          <div className='col-lg-6 col-md-6 col-12 mb-3'>
                            <PayWithPaystack email={user.email} invoice={invoice.invoice_number} amount={invoice.amount_due} authtoken={auth.token} role={auth.role} />
                          </div>

                          <div className='col-lg-6 col-md-6 col-12 mb-3'>
                            <PayWithFlutterwave />
                          </div>

                        </div>

                      </div>

                      <div id="loadingDiv" className='d-none'>
                        <div style={{ zIndex: 9999, backgroundColor: 'rgba(0,0,0,.50)' }} className="position-absolute h-100 vw-100 top-0 start-0 d-flex justify-content-center align-items-center text-light">
                          <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                          <span className='ms-2 align-middle'>loading...</span>
                        </div>
                      </div>

                    </>
                }

              </div>
            </div>
        }
      </section>
    </>
  );
}

export default PayInvoice;
