import { query } from ".";


export const alert = (message, swatch = 'success', autohide = true) => {
  let randID = Math.floor(Math.random() * 99999);
  let swatchColor = swatch;
  if (swatch === "error") {
    swatchColor = "danger";
  }
  if (swatch === "light") {
    swatch = "info";
  }
  let elem = `
  <div id='alert${randID}' style="" class="toast show">
    <div class="toast-body alert-${swatchColor}">
      <div class="d-flex">
        <strong class="me-auto title">${swatch.toString().toUpperCase()}</strong>
        <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close" onclick="document.getElementById('alert${randID}').outerHTML = '';" />
      </div>
      <hr class='my-2' />
      <span class='message'>${message}</span>
    </div>
  </div>`;

  // insert the element in the alert container
  let alertContainer = query('#alertContainer')[0];
  alertContainer.insertAdjacentHTML('beforeEnd', elem);

  if (autohide === true) {
    setTimeout(() => {
      let alert = query(`#alert${randID}`)[0];
      if (alert !== undefined) {
        alert.outerHTML = "";
      }
    }, 5000);
  }

}