import { useCallback, useEffect, useState } from 'react';
import { ChevronLeft, ChevronRight } from 'react-feather';
import { Link } from 'react-router-dom';
import { formatMoney, request } from '../../../../utils';
import './Programs.scss'

export const Programs = function () {

  const [slots, setSlots] = useState([])

  const getSlots = useCallback(async () => {
    try {
      let resp = await request('/api/slots', 'GET');

      if (resp.status === true) {
        setSlots(resp.data)
      }
    } catch (error) {
      console.log(error);
    }
  }, [])

  useEffect(() => {
    getSlots()
  }, [getSlots])

  return (
    <div className='__programs bg-white w-100 position-relative'>
      
      <div className='w-100 position-absolute top-30 d-flex justify-content-between px-lg-5'>
        <ChevronLeft strokeWidth={0.5} className='cursor-pointer bg-light btn btn-sm shadow-sm rounded-circle left-10' size={60} color='#17171C' onClick={() => {
          document.querySelectorAll('.__programs_container')[0].scrollBy({
            left: -360,
            top: 0,
            behavior: 'smooth'
          })
        }} />
        <ChevronRight strokeWidth={0.5} className='cursor-pointer bg-light btn btn-sm shadow-sm rounded-circle right-10' size={60} color='#17171C' onClick={() => {
          document.querySelectorAll('.__programs_container')[0].scrollBy({
            left: 360,
            top: 0,
            behavior: 'smooth'
          })
        }} />
      </div>

      <div className='container w-100 h-100 d-flex align-items-center justify-content-center'>

        <div className='text-center w-100'>

          <h2 className='bruno-ace text-primary mb-lg-5 mb-md-4 mb-2'>PROGRAMS</h2>
          <a className='__namelink' href='/' name="slot-programs">{ }</a>

          <div className='__programs_container py-lg-3 py-md-3 py-4 pb-lg-5 pb-md-4 pb-3 d-flex justify-content-between'>

            {
              slots.length > 0 ?
                slots.map(slot => (
                  <div key={slot.id} className={`__program ${slot.id < 6 ? 'me-lg-5 me-md-4 me-4' : ''} bg-light border border-primary rounded`}>
                    <div className='h-100 w-100 p-lg-5 p-md-4 p-4 d-flex flex-column justify-content-between align-items-center'>

                      <div className='w-100 mb-lg-4 mb-md-3 mb-2'>
                        <h6 className='fs-5'>{slot.program.toUpperCase()}</h6>
                        <hr className='__underline w-100 my-lg-2 my-md-2 my-2 bg-primary' />
                      </div>

                      <div className='h-100 d-flex flex-column justify-content-start'>
                        <h5 className='text-primary fs-2 bruno-ace'>{formatMoney(slot.cost, 0)}</h5>

                        <p className='my-lg-3 my-md-3 my-3'>To Start</p>

                        <p>{slot.benefits}</p>

                      </div>

                      <div className='w-100'>
                        <Link to={`/register?slotId=${slot.id}`} className='btn btn-primary text-white fs-6 py-2 px-3 w-100'>Select</Link>
                      </div>

                    </div>
                  </div>
                ))
                :
                <div>Loading Slot Programs...</div>
            }

          </div>

          <div className='w-100 w-md-70 w-lg-80 mx-auto'>
            <p className='fs-6 text-center text-dark mt-lg-5 mt-md-4 mt-4'>PLEASE NOTE THAT REGISTRATION FEE MAY VARY FROM TIME TO TIME ACCORDING TO THE OPERATING ENVIRONMENT.</p>
          </div>

        </div>

      </div>

    </div>
  )
}