import { request } from "../utils";
import create from 'zustand'
import { devtools } from 'zustand/middleware'

const defaultUser = {
  isset: false,
  notifications: 0,
  accountname: '',
  accountnumber: '',
  bankname: '',
  created_at: '',
  email: '',
  favourite_sport: '',
  favourite_team_local: '',
  favourite_team_foreign: '',
  favourite_team_international: '',
  favourite_team_continental: '',
  favourite_team_worldcup: '',
  favourite_team_olympic: '',
  firstname: '',
  lastname: '',
  member_id: '',
  middlename: '',
  nationality: '',
  nextofkin_name: '',
  nextofkin_residential_address: '',
  nextofkin_telephone: '',
  occupation: '',
  profile_picture: null,
  referral_code: '',
  residential_address: '',
  telephone: '',
  verification_status: '',
  payment_status: '',
  slot_program: '',
}


export const useUserStore = create(
  devtools(
    (set, get) => ({
      user: defaultUser,

      boot: async () => {
        console.log('user booting...')

        new Promise((resolve, reject) => {
          let user = JSON.parse(localStorage.getItem('User')) ?? defaultUser;
          resolve(user);
        })
          .then(user => {
            set({ user: { ...user } })
          })
      },

      setUser: (user) => {
        localStorage.setItem('User', JSON.stringify({
          ...user
        }));

        set({
          user: {
            ...user
          }
        })
      },

      getProfile: async (authToken) => {
        try {
          let headers = new Headers();
          headers.append('AuthToken', authToken);

          let resp = await request('/api/profile', 'GET', {}, headers);

          if (resp.status === true) {
            localStorage.setItem('User', JSON.stringify({
              ...resp.data,
              isset: true
            }));

            set({
              user: {
                ...resp.data,
                isset: true
              }
            })
          }
        } catch (error) {
          console.log(error);
        }
      }

    })
  )
);