import { query } from ".";

export const confirm = (message, onConfirm = () => { }, onCancel = () => { }) => {
  let randID = Math.floor(Math.random() * 99999);
  let elem = `
  <div id='confirm${randID}' style="" cl ass="toast show">
    <div class="rounded toast-body alert-light">
      <div>
        <p class="title">${message}</p>
      </div>
      <br cl ass='my-2' />
      <div class='d-flex justify-content-evenly w-100 mt-2'>
        <button id='confirm-cancel' class='btn btn-sm btn-danger text-light w-100'>Cancel</button>
        <button id='confirm-confirm' class='btn btn-sm btn-success text-light ms-2 w-100'>Confirm</button>
      </div>
    </div>
  </div>`;

  // insert the element in the confirm modal
  let confirmModal = query('#confirmModalContent')[0];
  confirmModal.innerHTML = elem;

  query('#confirmModalTrigger')[0].click();

  function newOnCancel() {
    onCancel();
    query('#confirmModalDismissTrigger')[0].click();
  }

  function newOnConfirm() {
    onConfirm();
    query('#confirmModalDismissTrigger')[0].click();
  }

  query(`#confirm-cancel`)[0].addEventListener('click', newOnCancel, false);
  query(`#confirm-confirm`)[0].addEventListener('click', newOnConfirm, false);

}